  getOwnerProperties(url, callback) {
    let thisModule = this;
    // if (this.ownerPropertiesXhr) {
    //   return;
    // }

    let cb = function (error, data) {
      // thisModule.ownerPropertiesXhr = null;
      if (!error) {
        data.results.forEach(function (prop) {
          prop.url = "/details?id=" + prop.id;
        });
      }
      callback(error, data);
    };
    url = url || this.UrlsObj["OWNER_PROPERTIES"] + "?limit=20";
    this.requests.send(url, "GET", {}, false, true, cb).subscribe(data => {
      cb(null, data)
    }, err => {
      cb(err, null)
    });
  };

  claimProperty(id, callback) {
    console.log(id)
    this.requests.send(this.UrlsObj["CLAIM_PROPERTY"], "POST", { apartment_id: id }, false, true, callback).subscribe(data => {
      callback(null, data)
    }, err => {
      callback(err, null)
    });
  };

