  getContracts(page, filters, callback) {
    // if (this.ownerContractsXhr) {
    // this.ownerContractsXhr.abort();
    // }

    console.log(filters)

    var cb = function (error, data) {
      this.ownerContractsXhr = undefined;
      callback(error, data);
    };

    let params = new URLSearchParams();
    for (let key in filters) {
      params.set(key, filters[key])
    }

    var url = this.UrlsObj["OWNER_CONTRACTS"] + "?offset=" + (page - 1) * this.itemsPerPage + "&" + params.toString();

    this.requests.send(url, "GET", {}, false, true, cb).subscribe(data => {
      console.log(data)
      callback(null, data)
    }, err => {
      callback(err, null)
    });
  };

