import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatDialogRef } from '@angular/material';
import { OwnerService } from 'src/app/services/owner/owner.service';

@Component({
	selector: 'app-payment-setup',
	templateUrl: './payment-setup.component.html',
	styleUrls: ['./payment-setup.component.css']
})
export class PaymentSetupComponent implements OnInit {

	form: FormGroup;
	submitting = false;
	cards: [];
	contracts = [];
	days = Array(30).fill(0).map((x, i) => i);
	countries = [];
	constructor(
		public dialogRef: MatDialogRef<PaymentSetupComponent>,
		private ownerService: OwnerService,
		private formBuilder: FormBuilder) {
		this.form = this.createForm();
	}
	ngOnInit() {}

	getRenterId(contractId: number) {
		for (let contract of this.contracts) {
			if (contract.id == contractId) {
				return contract.renter_id;
			}
		}
		return null;
	}

	submit() {
		this.submitting = true;
		const dobObject = new Date(this.form.value.dob);
		let dob = {
			day: dobObject.getDay(),
			month: dobObject.getMonth(),
			year: dobObject.getFullYear()
		}
		const id_number = this.form.value.id_number;
		const ssn_last_4 = id_number.substring(id_number.length - 4, id_number.length);

		const data = Object.assign({}, this.form.value, { dob }, { ssn_last_4 });
		this.ownerService.setupPayment(data, (err, res) => {
			this.submitting = false
			if (err) {
				return alert("Something went wrong while setting up payment")
			}
			alert("Pament method was added successfully");
			this.dialogRef.close(res);
		})
	}

	public createForm() {
		return this.formBuilder.group({
			first_name: [null, Validators.required],
			last_name: [null, Validators.required],
			dob: [null, Validators.required],
			id_number: [null],
			website: [null, Validators.required],
			country: ["US", Validators.required],
			email: [null, Validators.required],
			phone: [null, Validators.required],
			line1: [null, Validators.required],
			line2: [null],
			city: [null, Validators.required],
			state: [null],
			postal_code: [null, Validators.required],
			number: [null, Validators.required],
			exp_year: [null, Validators.required],
			exp_month: [null, Validators.required],
			cvc: [null, Validators.required],
			currency: ["USD", Validators.required],
		})
	}
	cancel() {
		this.dialogRef.close();
	}
	stringify(value) {
		return JSON.stringify(value || {})
	}

}
