export class Property{
    
    state
    description
    zillowRent
    latitude
    longitude
    city
    street
    zipCode
    zpid
    bedRooms
    bathRooms
    market
    constructor(options) {
        options = options || {};
        this.state = options.state || "";
        this.description = options.description || "";
        this.zillowRent = options.zillowRent || "";
        this.latitude = options.latitude || "";
        this.longitude = options.longitude || "";
        this.city = options.city || "";
        this.street = options.street || "";
        this.zipCode = options.zipCode || "";
        this.zpid = options.zpid || "";
        this.bedRooms = options.bedRooms || 0;
        this.bathRooms = options.bathRooms || 0;
        this.market = options.market || 0.0;
    };

    getCity = function () {
        return this.city;
    };

    getStreet = function () {
        return this.street;
    };

    getZipCode = function () {
        return this.zipCode;
    };

    getZpid = function () {
        return this.zpid;
    };

    getState = function () {
        return this.state;
    };

    getDescription = function () {
        return this.description;
    };

    getZillowRent = function () {
        if (this.zillowRent == "N/A") {
            return 0;
        }
        return this.zillowRent;
    };

    getLatitude = function () {
        return this.latitude;
    };

    getLongitude = function () {
        return this.longitude;
    };

    getBedRooms = function () {
        return this.bedRooms;
    };

    getBathRooms = function () {
        return this.bathRooms;
    };

    getMarket = function () {
        return this.market;
    };

    setCity = function (val) {
        this.city = val;
    };

    setStreet = function (val) {
        this.street = val;
    };

    setZipCode = function (val) {
        this.zipCode = val;
    };

    setZpid = function (val) {
        this.zpid = val;
    };

    setState = function (val) {
        this.state = val;
    };

    setDescription = function (val) {
        this.description = val;
    };

    setZillowRent = function (val) {
        this.zillowRent = val;
    };

    setLatitude = function (val) {
        this.latitude = val;
    };

    setLongitude = function (val) {
        this.longitude = val;
    };

    setBedRooms = function (val) {
        this.bedRooms = val;
    };

    setBathRooms = function (val) {
        this.bathRooms = val;
    };

    setMarket = function (val) {
        this.market = val;
    };

}
