import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material';
import { AddCardDialog } from '../../dialog/add-card/add-card';
import { RenterServiceService } from 'src/app/services/renterService/renter-service.service';

@Component({
	selector: 'app-pay-rent',
	templateUrl: './pay-rent.component.html',
	styleUrls: ['./pay-rent.component.css']
})
export class PayRentComponent implements OnInit {

	constructor(private dialog: MatDialog, private renterService: RenterServiceService) { }
	cardDetails = {
		number: null,
		exp_month: null,
		exp_year: null,
		cvc: null,
	};
	cards = [];
	stripeId: string | null = null;
	schedules = [];
	ngOnInit() {
		this.renterService.getStripeId((err, data) => {
			if (err) {
				this.stripeId = null;
				return;
			}
			this.stripeId = data;			
		});
		this.renterService.getRenterRentSchedule((err, data) => {
			if (err) {
				alert("Something went wrong while getting rent schedule");
				return;
			}
			this.schedules = data.results;
		});
	}

	getNextDateFromDay(day: number) {
		let date = new Date();
		if (date.getDate() > day) {
			date.setMonth((date.getMonth() + 1) % 12)
		}
		date.setDate(day);
		return date;
	}

	addCard() {
		const dialogRef = this.dialog.open(AddCardDialog, {
			width: '100vw',
			height: '100vh',
			data: { test: "test", cardDetails: this.cardDetails },

		});
		dialogRef.afterClosed().subscribe((result) => {
			if (result && result.status == "add") {
				this.cardDetails = result.cardDetails
				this.renterService.addCard(this.cardDetails, (error, data) => {
					// loaderService.hideLoader();
					if (error) {
						alert("Invalid card Details");
					} else {
						this.stripeId = data;
					}
				});
			}
		});
	}

	attachCard(schedule, card) {
		schedule.pendingCard = card;
	}

	approve(schedule) {
		const data = {
			approved: true
		}
		this.renterService.approveSchedule(schedule.id, data, (err, res) => {
			if (err) {
				return alert("Something went wrong while approving");
			}
			schedule.approved = true;
			schedule.renter_card = schedule.pendingCard;
			alert("approved");

		})
	}

	unApprove(schedule){
		const data = {
			approved: false
		}
		this.renterService.approveSchedule(schedule.id, data, (err, res) => {
			if (err) {
				return alert("Something went wrong while canceling");
			}
			schedule.approved = false;
			alert("Canceled");

		})
	}

	remove(schedule, index) {
		this.renterService.removeSchedule(schedule.id, (err, res) => {
			if (err) {
				return alert("Something went wrong while deleting");
			}
			this.schedules.splice(index, 1);
			alert("Deleted");

		})
	}

}
