import { Injectable } from '@angular/core';
import { RequestService } from './../Request/request.service'
import { Config } from "../../utils/Config";
import { Urls } from '../../utils/Urls'

@Injectable({
  providedIn: 'root'
})
export class DetailsRoomService {

  constructor(
    private request: RequestService
  ) { }

  ConfigObj = Config()
  UrlsObj = Urls(this.ConfigObj)

  get(id, callback) {
    this.request.send(this.UrlsObj["GET_ROOM_PROPERTIES_BY_ID"] + id, "GET", {}, false, true, callback).subscribe(data => {
      callback(null, data)
    }, err => {
      callback(err, null)
    });;
  };
}
