
import { Component, Inject } from "@angular/core"
import { MatDialogRef, MAT_DIALOG_DATA } from "@angular/material"
import { OwnerService } from "../../../services/owner/owner.service";
import * as Quill from 'quill';


@Component({
    selector: 'owner-send-contract',
    templateUrl: './owner-send-contract.html'
})
export class OwnerSendContractDialog {
    contractForm: any
    currentTab: any
    quill: any = null
    renter_name: any
    start_date: any;
    startMinDate: any = new Date()
    startMaxDate
    constructor(
        public ownerService: OwnerService,
        public dialogRef: MatDialogRef<OwnerSendContractDialog>,
        @Inject(MAT_DIALOG_DATA) data: any
    ) {
        this.contractForm = data.contractForm;
        this.currentTab = data.currentTab;
        this.renter_name = data.renter_name;
    }

    closeDialog(answer): void {
        if (answer) {
            this.dialogRef.close(this.quill);
        } else {
            this.dialogRef.close();
        }
    }

    prevTab() {
        if (this.currentTab == 2) {
            this.contractForm.validDetails = false;
        }

        this.currentTab -= 1;
    };

    nextTab(form) {
        console.log(form)
        if (form && form.valid) {
            this.contractForm.validDetails = true;
        }else if(form){
            return
        }

        this.currentTab += 1;
        if (form) {
            console.log("quill create")
            setTimeout(() => {
                if (!this.quill) {
                    this.quill = new Quill('#editor-container', {
                        modules: {
                            toolbar: [
                                [{ header: [1, 2, false] }],
                                ['bold', 'italic', 'underline'],
                                ['image', 'code-block']
                            ]
                        },
                        placeholder: 'Compose an epic...',
                        theme: 'snow'  // or 'bubble'
                    });
                }
            }, 500)
        }
    };

    disableReviewTab = function () {
        this.contractForm.validDetails = false;
    };
}

