
import { Component, Inject } from "@angular/core"
import { MatDialogRef, MAT_DIALOG_DATA } from "@angular/material"
import { OwnerService } from "../../../services/owner/owner.service";

@Component({
    selector: 'advanced-property-edit',
    templateUrl: './advanced-property-edit.html'
})
export class AdvancedPropertyEditDialog {
    roomDetailsCopy: any = [];
    constructor(
        public ownerService: OwnerService,
        public dialogRef: MatDialogRef<AdvancedPropertyEditDialog>,
        @Inject(MAT_DIALOG_DATA) data: any
    ) {
        this.roomDetailsCopy = data.roomDetailsCopy;
     }

    closeDialog(submit): void {
        if(submit){
            console.log(this.roomDetailsCopy)
            this.dialogRef.close(this.roomDetailsCopy);
        }else{
            this.dialogRef.close();
        }
    }

    addRoom() {
        this.roomDetailsCopy.push({
            'id': 0,
            'is_occupied': false,
            'rent': 0,
            'room_label': " Room " + (this.roomDetailsCopy.length + 1)
        });
    };

    deleteRoom(index) {
        console.log(index)
        this.roomDetailsCopy.splice(index, 1);
    };
    unAssignRoom(index) {
        var data = {
            "property_renter_id": this.roomDetailsCopy[index].property_renter_id,
            "status": 4
        };
        // loaderService.showLoader(" Unassigning room ");
        this.ownerService.editPropertyRenterService(data, function (error1, data1) {
            // loaderService.hideLoader();
            if (!error1) {
                this.roomDetailsCopy[index].renter_first_name = null;
                this.roomDetailsCopy[index].renter_last_name = null;
                this.roomDetailsCopy[index].is_occupied = false;
                this.roomDetails[index] = this.roomDetailsCopy[index];
            }
        });
    };
    errorText = null;

}
