import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { MatDialogModule, MatRadioModule } from '@angular/material';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { SignUpComponent } from './sign-up.component';
import { FormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import {
  SocialLoginModule,
  AuthServiceConfig,
  FacebookLoginProvider,
  GoogleLoginProvider
} from "angular-6-social-login-v2";

import { Config } from './../../../utils/Config'

declare var FB: any;
export function getAuthServiceConfigs() {
  let configDetails = Config();

  FB.init({
    appId: Config["FB_APP_ID"],
    cookie: true,  // enable cookies to allow the server to access
    // the session
    xfbml: true,  // parse social plugins on this page
    version: 'v2.8' // use graph api version 2.8
  });

  let config = new AuthServiceConfig(
    [
      {
        id: FacebookLoginProvider.PROVIDER_ID,
        provider: new FacebookLoginProvider(configDetails["FB_APP_ID"])
        // provider: new FacebookLoginProvider("2213664865616180")
      },
      {
        id: GoogleLoginProvider.PROVIDER_ID,
        
        provider: new GoogleLoginProvider(configDetails["GOOGLE_APP_ID"])
        // provider: new GoogleLoginProvider("6613472973-697nc3ncmg76g8mngnqcm6213iekcgqn.apps.googleusercontent.com")
        // provider: new GoogleLoginProvider("487670095053-ce18969sr926rdm1fkbgmtmdmeqlkrr3.apps.googleusercontent.com")
      }
    ]
  );
  return config;
}

@NgModule({
  declarations: [
    SignUpComponent
  ],

  imports: [
    BrowserModule,
    MatDialogModule,
    BrowserAnimationsModule,
    MatRadioModule,
    FormsModule,
    SocialLoginModule,
    HttpClientModule
  ],
  providers: [{
    provide: AuthServiceConfig,
    useFactory: getAuthServiceConfigs
  }],
  bootstrap: [SignUpComponent],
  entryComponents: [SignUpComponent]
})

export class SignUpModule { }
