
import { Component, Inject } from "@angular/core"
import { MatDialogRef, MAT_DIALOG_DATA } from "@angular/material"
import { OwnerService } from "../../../services/owner/owner.service";

@Component({
    selector: 'pay-token',
    templateUrl: './pay-token.html'
})
export class PayTokenDialog {
    // roomDetailsCopy: any = [];
    selectedCardId: any
    cards: any
    constructor(
        public ownerService: OwnerService,
        public dialogRef: MatDialogRef<PayTokenDialog>,
        @Inject(MAT_DIALOG_DATA) data: any
    ) {
        this.cards = data.cards;
    }

    closeDialog(answer): void {
        if (answer) {
            this.dialogRef.close({ status: answer, selectedCardId: this.selectedCardId });
        } else {
            this.dialogRef.close();
        }
    }
}
