import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AddPropertyComponent } from './components/main/add-property/add-property.component'
import { DashboardComponent } from './components/main/dashboard/dashboard.component'
import { PropertiesComponent } from './components/owner/properties/properties.component'
import { SearchPropertyComponent } from './components/main/search-property/search-property.component';
import { EditPropertyComponent } from './components/main/edit-property/edit-property.component'
import { ReportsComponent } from './components/main/reports/reports.component'
import { PropertyDetailsComponent } from './components/main/property-details/property-details.component'
import { RenterReportComponent } from './components/main/renter-report/renter-report.component'
import { CollectRentComponent } from './components/main/collect-rent/collect-rent.component';
import { ComingSoonComponent } from './components/main/coming-soon/coming-soon.component';
import { PayRentComponent } from './components/main/pay-rent/pay-rent.component';
const routes: Routes = [
  { path: '', component: DashboardComponent, pathMatch: 'full' },
  { path: 'add-property', component: AddPropertyComponent },
  { path: 'spaces', component: PropertiesComponent },
  { path: 'search-property', component: SearchPropertyComponent },
  { path: 'edit-property', component: EditPropertyComponent },
  { path: 'reports', component: ReportsComponent },
  { path: 'property-details', component: PropertyDetailsComponent },
  { path: 'collect-rent', component: CollectRentComponent },
  { path: 'renter-report', component: RenterReportComponent },
  { path: 'pay-rent', component: PayRentComponent },
  { path: 'coming-soon', component: ComingSoonComponent },

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
