import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import { CookieService } from 'ngx-cookie-service'

@Injectable({
  providedIn: 'root'
})

export class userDataTemplate {
  email: string
  image: string
  id: string
  name: string
  provider: string
  token: string
  authenticated: boolean = false;
  userType: string;
  isOwner: boolean;
  isRenter: boolean;

  constructor() { }
}

export class UserDataService {
  user = null;
  private authenticationState = new Subject<userDataTemplate>();
  authenticationState$ = this.authenticationState.asObservable();
  announceauthenticationStateChange(user:userDataTemplate) {
    if(!user){
      user = new userDataTemplate();
    }

    this.user = user;
    this.cookie.set("userdata", JSON.stringify(user));
    console.log("authentication state changed");
    this.authenticationState.next(user);
  }

  private showLogin = new Subject<null>();
  showLogin$ = this.showLogin.asObservable();
  announceshowLoginChange() {
    console.log("Showing state changed");
    this.showLogin.next();
  }


  constructor(private cookie: CookieService) { }
}
