import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { MatDialogRef } from '@angular/material';
import { OwnerService } from 'src/app/services/owner/owner.service';

@Component({
	selector: 'app-add-rent-schedule',
	templateUrl: './add-rent-schedule.component.html',
	styleUrls: ['./add-rent-schedule.component.css']
})
export class AddRentScheduleComponent implements OnInit {
	form: FormGroup;
	submitting = false;
	cards: [];
	contracts = [];
	days = Array(30).fill(0).map((x, i) => i);
	constructor(
		public dialogRef: MatDialogRef<AddRentScheduleComponent>,
		private ownerService: OwnerService,
		private formBuilder: FormBuilder) {
		this.form = this.createForm();
	}
	ngOnInit() {
		this.ownerService.getContracts(1, [], (err, data) => {
			if (err) {
				alert("Something went wrong while getting cards");
				return;
			}
			this.contracts = data.results;
		});
	}

	getRenterId(contractId: number) {
		for (let contract of this.contracts) {
			if (contract.id == contractId) {
				return contract.renter_id;
			}
		}
		return null;
	}

	submit() {
		this.submitting = true
		let rentSchedule = Object.assign({}, this.form.value, { renter_id: this.getRenterId(this.form.value.contract_id) });
		this.ownerService.addRentSchedule(rentSchedule, (err, res) => {
			this.submitting = false
			if (err) {
				return alert("Something went wrong when adding rent schedule")
			}
			alert("Added new rent schedule");
			this.dialogRef.close(res);
		})
	}

	public createForm() {
		return this.formBuilder.group({
			amount: [null, Validators.required],
			contract_id: [null, Validators.required],
			date: [null, Validators.required]
		})
	}
	cancel() {
		this.dialogRef.close();
	}

}
