import { Component, OnInit } from '@angular/core';
import { OwnerService } from './../../../services/owner/owner.service'
import { Router } from '@angular/router';

let thisModule;

@Component({
  selector: 'app-properties',
  templateUrl: './properties.component.html',
  styleUrls: ['./properties.component.css']
})

export class PropertiesComponent implements OnInit {
  // ownerProperties:any = []
  ownerProperties: any = [];
  pendingProperties: any = [];
  next: any = null;
  nextPendingProperties: any = null;
  fetched: any = false;

  constructor(private ownerService: OwnerService, private router: Router) { thisModule = this }

  ngOnInit() {
    thisModule.getProperties();
    thisModule.getPendingProperties();
  }

  getPendingProperties() {
    let thisModule = this;
    thisModule.ownerService.getPendingProperties(null, function (error, data) {
      if (thisModule.fetched) {
        // thisModule.loaderService.hideLoader();
        thisModule.fetched = false;
      } else {
        thisModule.fetched = true;
      }

      if (!error) {
        thisModule.pendingProperties = data.results || [];
        console.log(thisModule.pendingProperties,data)
        thisModule.nextPendingProperties = data.next;
        // $(window).scroll(function () {
        // if ($(window).scrollTop() + $(window).height() > $(document).height() - 1000) {
        // if (thisModule.nextPendingProperties) {
        // thisModule.getMorePendingProperties();
        // }
        // }
        // });
      }
    });
  }
  getProperties = function () {
    let thisModule = this;
    thisModule.ownerService.getOwnerProperties(null, function (error, data) {
      // if (thisModule.fetched) {
        // thisModule.loaderService.hideLoader();
        // thisModule.fetched = false;
      // } else {
        // thisModule.fetched = true;
      // }
      if (!error) {
        thisModule.ownerProperties = data.results || [];
        console.log(thisModule.ownerProperties)
        thisModule.next = data.next;
        // $(window).scroll(function () {
        // if ($(window).scrollTop() + $(window).height() > $(document).height() - 1000) {
        // if (thisModule.next) {
        // thisModule.getMoreProperties();
        // }
        // }
        // });
      }
    });
  }

  onScroll() {
    console.log("On Scroll")
    if (thisModule.next) {
      thisModule.getMoreProperties();
    }

    if (thisModule.nextPendingProperties) {
      thisModule.getMorePendingProperties();
    }
  }
  getMoreProperties = function () {
    thisModule.ownerService.getOwnerProperties(thisModule.next, function (error, data) {
      if (!error) {
        thisModule.ownerProperties = thisModule.ownerProperties.concat(data.results);
        thisModule.next = data.next;
      }
    });
  };

  claimProperty = function (id) {
    thisModule.ownerService.claimProperty(id, function (error, data) {
      if (!error) {
        thisModule.getPendingProperties();
        thisModule.getProperties();
      }
    })
  };

  getMorePendingProperties = function () {
    thisModule.ownerService.getPendingProperties(thisModule.nextPendingProprties, function (error, data) {
      if (!error) {
        thisModule.pendingProperties = thisModule.pendingProperties.concat(data.results);
        thisModule.nextPendingProprties = data.next;
      }
    });
  };

  gotoEdit(id){
    this.router.navigate(["/edit-property"], { queryParams: {id:id} })
  }

  // thisModule.$on('event:auth', function (event, data) {
  //   if (data.authenticated) {
  //     thisModule.loaderService.showLoader("Fetching Properties");
  //     thisModule.getPendingProperties();
  //     getProperties();
  //   }
  // });

}
