import { Injectable } from '@angular/core';
import { Config } from "../../utils/Config";
import { Urls } from '../../utils/Urls'
import { RequestService } from '../Request/request.service'

@Injectable({
  providedIn: 'root'
})
export class DetailsUnitNameService {

  constructor(private requests: RequestService) { }
  UrlsObj = Urls(Config())

  get(id, callback) {
    this.requests.send(this.UrlsObj["GET_UNIT_NAMES_BY_ID"] + id + "/", "GET", {}, false, true, callback).subscribe(data=>{
      callback(null, data)
    }, err=>{
      callback(err, null)
    });
  };
}
