import { Component, OnInit, ViewChild, NgZone, Inject } from '@angular/core';
import { NgForm } from '@angular/forms'
import { Location, LocationStrategy, PathLocationStrategy } from '@angular/common';
import { RenterServiceService } from '../../../services/renterService/renter-service.service'
import { UploadEvent, UploadFile, FileSystemFileEntry, FileSystemDirectoryEntry } from 'ngx-file-drop';
import { UserDataService, userDataTemplate } from '../../../services/user-data.service'
import { CommonService } from '../../../services/common/common.service'
import { OwnerService } from '../../../services/owner/owner.service'
import { MatDialogRef, MatDialog, MAT_DIALOG_DATA } from '@angular/material'
import { CookieService } from 'ngx-cookie-service'
import { Subscription } from 'rxjs';
import { Property } from '../../../models/Property'
import { MapModel } from '../../../models/Map'
import { AlertDialog } from './../../dialog/alert/alert.component'
import { Router } from "@angular/router"
import { FileUploadHelper } from './../../../utils/FileUploadHelper'
import { AuthServiceService } from '../../../services/auth/auth.service'
// import { } from 'googlemaps';
declare var $;
declare const google: any;

@Component({
  selector: 'app-add-property',
  templateUrl: './add-property.component.html',
  styleUrls: ['./add-property.component.css'],
  providers: [Location, { provide: LocationStrategy, useClass: PathLocationStrategy }]
})


export class AddPropertyComponent implements OnInit {

  mapForAddProperty: any;
  markerForAddProperty: any;
  searchElementRef: any;
  // files

  oldDropFiles: any
  upload(files, buttonUpload) {
    if (!Array.isArray(files)) {
      files = [files]
    }

    let thisModule = this;
    if (!thisModule.oldDropFiles) {
      thisModule.oldDropFiles = files;
    } else {
      if (thisModule.oldDropFiles == files) {
        return;
      } else {
        thisModule.oldDropFiles = files;
      }
    }
    if (files && files.length) {
      if (!thisModule.s3Details) {
        this.showLogin();
        // authService.showLogin($timeout, function () {
        // thisModule.getS3Details(thisModule.upload, files)
        // });

        return;
      }
      thisModule.tempUnitData.newImagesCount = thisModule.tempUnitData.images.length;
      $.each(files, function (_, value) {
        console.log(_, value)
        var reader = new FileReader();
        reader.onload = function (e) {
          console.log(e)
          var options = {
            file: "",
            signature: thisModule.s3Details.SIGNATURE,
            s3_url: "https://propertyowneruploads.s3.amazonaws.com/",
            policy: thisModule.s3Details.POLICY,
            path: "propertyImages/",
            index: null
          };
          thisModule.tempUnitData.thumbNails.push({
            index: thisModule.tempUnitData.newImagesCount,
            src: e.currentTarget["result"],
            isUpload: true
          });

          if (buttonUpload) {
            options.file = value[0];
          } else {
            options.file = value;
          }
          options.index = thisModule.tempUnitData.newImagesCount;
          var helper = new FileUploadHelper(options);
          helper.upload(function (imageId) {
            if (thisModule.tempUnitData.thumbNails[helper.options.index]) {
              console.log(thisModule.s3Details);
              thisModule.tempUnitData.images.push(thisModule.s3Details.S3_URL + "propertyowneruploads/propertyImages/" + imageId);
              console.log(thisModule.tempUnitData.images);
            }
          });
          thisModule.tempUnitData.newImagesCount++;
        };

        if (buttonUpload) {
          reader.readAsDataURL(value[0]);
          // options.file = value[0];
        } else {
          reader.readAsDataURL(value);
        }
      });
    }
  };

  removeImage = function (id) {
    this.tempUnitData.thumbNails.splice(id, 1);
    this.tempUnitData.images.splice(id, 1);
  };

  public selectedFiles(event) {
    this.upload(event.files, true)
  }
  public dropped(event: UploadEvent) {
    console.log("File Dropped");

    this.uploadedImages = event.files;
    for (const droppedFile of event.files) {

      // Is it a file?
      if (droppedFile.fileEntry.isFile) {
        const fileEntry = droppedFile.fileEntry as FileSystemFileEntry;
        fileEntry.file((file: File) => {

          // Here you can access the real file
          console.log(droppedFile.relativePath, file);
          this.upload(file, false)

        });
      } else {
        // It was a directory (empty directories are added, otherwise only files)
        const fileEntry = droppedFile.fileEntry as FileSystemDirectoryEntry;
        console.log(droppedFile.relativePath, fileEntry);
      }
    }
  }

  public fileOver(event) {
    console.log(event);
  }

  public fileLeave(event) {
    console.log(event);
  }
  // end files

  showLogin() {
    this.userDataService.announceshowLoginChange();
  }

  showPrices(): void {
    const dialogRef = this.dialog.open(RentSplitDialog, {
      width: '250px',
      data: this.tempUnitData
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      if (result) {
        this.tempUnitData.unitPrice = result.unitPrice;
        this.tempUnitData.roomPrices = result.roomPrices;
      }
    });
  }

  showAlertPropertyCreated() {
    const dialogRef = this.dialog.open(AlertDialog, {
      width: '280px',
      data: { content: "Property created successfully!!!" }
    });

    dialogRef.afterClosed().subscribe(result => {
      this.router.navigate(['/spaces'])
    });
  }

  location: Location

  citySearchValue: any = ""
  searchProperty: any = ""
  // tab - 1
  addPropertyDetails: any = {
    listingType: "0",
    propertyType: "1"
  };

  currentStepDisplay: any = 1;
  stepNames: any = [
    ["Property Type", "Property Type"],
    ["Location", "Location"],
    ["Floorplans/Units", "Lisiting Details"],
    ["Floorplan/Unit details", "Summary"],
    ["Summary", ""]
  ]

  data = {
    totalTypes: 1,
    units: [],
    newImages: [],
    newImagesCount: 0,
    currentUnit: 1,
    summaryMode: false,
    roomAvailability: {},
    propertyName: "",
    unitNames: {},
    unitFloors: {},
    thumbNails: [],
    currentStep: 0,
    maxStep: 0
  };

  getTempUnitData() {
    return {
      name: "",
      units: 1,
      bedRooms: 1,
      bathRooms: 1,
      details: [],
      rentSplit: -1,
      amenities: [],
      unitPrice: 1,
      roomPrices: [],
      images: this.data.newImages.slice(0),
      thumbNails: this.data.thumbNails.slice(0),
      newImagesCount: this.data.newImages.length
    };
  }

  tempUnitData = this.getTempUnitData();



  floorNo = 1;
  availabiltyErrorText: null;
  inputValidationError: null;
  uploadedImages: UploadFile[] = [];
  placeDetails: any = { name: "", city: "", state: "", zipCode: "" };
  locationDetails = null;
  selectedProperty = { city: "", state: "", zipCode: "" };
  amenities = null;
  loginStateSubscription: Subscription
  user: userDataTemplate;
  constructor(
    location: Location,
    private renterService: RenterServiceService,
    private zone: NgZone,
    private userDataService: UserDataService,
    public dialog: MatDialog,
    private commonService: CommonService,
    private ownerService: OwnerService,
    private cookie: CookieService,
    private router: Router,
    private authServiceService: AuthServiceService
  ) {
    this.location = location;
    let thisModule = this;
    this.loginStateSubscription = this.userDataService.authenticationState$.subscribe(data => {
      console.log(data);
      thisModule.user = data;
      this.getS3Details(null, null, null);
    })
  }

  onTabChanged(e) {
    this.currentStepDisplay = e.index;
    this.data.currentStep = e.index;
  }

  ngOnInit() {
    console.log("Working")

    this.getS3Details(null, null, null);

    this.renterService.getAmenities((err, data) => {
      if (err) {
        console.log("err", err)
      } else {
        this.amenities = data.results
        console.log(data)
      }
    })

    this.initMap();
  }

  initMap() {
    var waitAndCheck = function () {
      setTimeout(function () {
        check();
      }, 1000);
    };

    var check = function () {
      if (document.getElementById("map")) {
        initializeMap();
      } else {
        waitAndCheck();
      }
    };

    let initializeMap = () => {
      let input = (document.getElementById('search-input'));

      let map = new google.maps.Map(document.getElementById("map"), {
        center: new google.maps.LatLng(38.916224, -77.0462604),
        zoom: 15
      });
      let marker = new google.maps.Marker({
        position: new google.maps.LatLng(38.916224, -77.0462604),
        map: map
      });

      this.mapForAddProperty = map;
      this.markerForAddProperty = marker;

      let options = {
        componentRestrictions: { country: "us" }
      };
      let autocomplete = new google.maps.places.Autocomplete(input, options);
      (this.searchElementRef.nativeElement, {
        types: ["address"]
      });

    };


    check();
  }

  setAddress(addrObj) {
    //We are wrapping this in a NgZone to reflect the changes
    //to the object in the DOM.
    console.log(addrObj)
    let myLatLng = { lat: addrObj.geometry.location.lat(), lng: addrObj.geometry.location.lng() };
    let map = new google.maps.Map(document.getElementById('map'), {
      zoom: 12,
      center: myLatLng
    });

    let marker = new google.maps.Marker({
      position: myLatLng,
      map: map,
      title: 'Hello World!'
    });

    this.zone.run(() => {
      let mapModel = new MapModel();
      this.placeDetails = mapModel.extractPlaceDetails(addrObj);
    });
  }

  nextStep() {

    let thisModule = this;
    //Hack to handle bug in md-tabs
    setTimeout(function () {
      // if (currentStep === this.data.currentStep) {
      //   this.nextStep();
      // }
      window.scroll(0, 0);
      // let currentStep = this.data.currentStep;
      console.log(thisModule)
      thisModule.data.currentStep += 1;
      thisModule.data.maxStep = Math.max(thisModule.data.maxStep, thisModule.data.currentStep);
      thisModule.updateCurrentStep(thisModule.data.currentStep);

    }, 10);
  };

  nextUnit = function () {
    var errMsg = this.validateUnit();

    if (!errMsg) {
      this.data.units[this.data.currentUnit - 1] = this.tempUnitData;
      this.data.currentUnit += 1;
      window.scrollTo(0, 0);
      if (this.data.units[this.data.currentUnit] && this.data.units[this.data.currentUnit].name) {
        this.tempUnitData = this.data.units[this.data.currentUnit - 1];
      } else {
        this.resetUnitData();
      }
      this.updateSelectAmenitiesList(this.tempUnitData.amenities);
    }
  };

  resetUnitData = function () {
    this.tempUnitData = this.getTempUnitData();

    this.resetSelectedAmenities();
  };

  resetSelectedAmenities = function () {
    if (this.amenities) {
      this.amenities.forEach(function (amenity) {
        amenity.selected = false;
      });
    }
  };

  previousUnit = function () {
    scrollTo(0, 0);
    this.data.units[this.data.currentUnit - 1] = this.tempUnitData;
    this.data.currentUnit -= 1;
    this.tempUnitData = this.data.units[this.data.currentUnit - 1];
    this.updateSelectAmenitiesList(this.tempUnitData.amenities);
  };

  updateSelectAmenitiesList = function (list) {
    list.forEach(function (amenity) {
      this.amenities[this.amenities.indexOf(amenity)].selected = true;
    });
  };

  updateCurrentStep = function (data) {
    if (data <= this.data.maxStep) {
      this.data.currentStep = data;
      this.currentStepDisplay = data + 1;
    }
  };

  previousStep = function () {
    if (this.data.currentStep > 1) {
      this.data.currentStep = this.data.currentStep - 1;
      this.currentStepDisplay = this.data.currentStep - 1;
    }
  };

  savePropertyPartial() {
    var data = {
      data: null,
      uploadedImages: null,
      addPropertyDetails: null,
      tempUnitData: null,
      placeDetails: null,
      locationDetails: null,
      selectedProperty: null,
      searchText: null,
    };

    data.data = this.data;
    data.uploadedImages = this.uploadedImages;
    data.addPropertyDetails = this.addPropertyDetails;
    data.tempUnitData = this.tempUnitData;
    data.placeDetails = this.placeDetails;
    data.locationDetails = this.locationDetails;
    data.selectedProperty = this.selectedProperty;
    data.searchText = $("#search-input").val();

    // ownerService.savePropertyPartial(JSON.stringify(data), function (error, data) {
    //   window.location.href = "/home";
    // });
  };


  detaisError: any;
  validateUnit() {
    console.log(this.tempUnitData)
    let unit = this.tempUnitData
    let errMsg;
    this.detaisError = null;

    if (this.addPropertyDetails.listingType != 1 && !unit.name) {
      errMsg = "Unit name is required.";
    } else if (unit.amenities.length == 0) {
      errMsg = "Unit amenities are required.";
    } else if (unit.rentSplit != 0 && unit.rentSplit != 1) {
      errMsg = "Invalid unit price";
    } else if (unit.rentSplit == 0 && unit.unitPrice <= 0) {
      errMsg = "Invalid unit price";
    } else if (unit.rentSplit == 1) {
      if (unit.roomPrices.length != unit.bedRooms) {
        errMsg = "Invalid room prices";
      } else {
        let roomPriceValid = true;

        unit.roomPrices.forEach(function (val) {
          if (!val) {
            roomPriceValid = false;
          }
        });

        if (!roomPriceValid) {
          errMsg = "Invalid room prices";
        }
      }
    }
    else if (unit.images.length == 0) {
      errMsg = "Unit images are required.";
    }

    this.detaisError = errMsg;
    return errMsg;
  };

  goToSummary = function () {
    console.log("summary");
    this.uploadedImages = [];
    this.data.units[this.data.totalTypes - 1] = this.tempUnitData;
    for (var i = 0; i < this.data.units.length; i++) {
      for (var j = 0; j < this.data.units[i].images.length; j++) {
        var imgSrc = this.data.units[i].images[j];
        this.uploadedImages.push(imgSrc);
      }
    }

    let totalUnitCount = 0;

    this.data.units.forEach(function (unitDetails) {
      totalUnitCount += unitDetails.units;
    });
    this.nextStep();
  };



  s3Details: any = null;
  getS3Details(callback, callbackData, callbackS3) {
    let thisModule = this;
    this.commonService.getS3Details(function (error, data) {
      console.log(data)
      if (!error) {
        thisModule.s3Details = data;
        if (callbackS3) {
          callbackS3(null, data)
        }
        if (callback) {
          callback(callbackData);
        }
      } else {
        if (callbackS3) {
          callbackS3(error, null)
        }
      }
    })
  };
  viewSummary() {
    var errMsg = this.validateUnit();
    if (!errMsg) {
      console.log(this.s3Details)
      if (!this.s3Details) {
        console.log(this.cookie.get('userdata'))
        if (this.cookie.get('userdata')) {
          this.getS3Details(null, null, (error, data) => {
            this.goToSummary();
          })
        }
        else {
          this.showLogin();
        }
      } else {
        this.goToSummary();
      }
    }
  };

  updateSelectedAmenities(amenity, value) {
    if (!value) {
      this.tempUnitData.amenities.splice(this.tempUnitData.amenities.indexOf(amenity), 1);
    } else {
      this.tempUnitData.amenities.push(amenity);
    }

    amenity.selected = value;
  };

  preSubmit = function () {
    if (this.commonService.isRenterLogin()) {
      this.providerOwnerEmail();
    } else {
      this.fillAvailabilityForm();
    }
  };

  preObject: any = {
    ownerEmail: ""
  }
  ownerEmail: any;
  providerOwnerEmail() {
    const dialogRef = this.dialog.open(ProviderOwnerEmailDialog, {
      width: '250px'
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed', result);
      if (result) {
        this.ownerEmail = result;
        if (this.addPropertyDetails.listingType == 0) {
          for (var i = 0; i < this.data.units.length; i++) {
            var unit = this.data.units[i];
            this.data.unitNames[i] = [];
            this.data.unitFloors[i] = [];
            for (var j = 0; j < unit.units; j++) {
              this.data.unitNames[i][j] = "Unit " + (i + j + 1);
              this.data.unitFloors[i][j] = 1;
            }
          }

          this.addMultipleProperties();
        } else {
          this.floorNo = 1;
          this.addProperty();
        }


        // this.tempUnitData.unitPrice = result.unitPrice;
        // this.tempUnitData.roomPrices = result.roomPrices;
      }
    });
  }

  fillAvailabilityForm() {
    let thisModule = this;
    const dialogRef = this.dialog.open(FillAvailabilityFormDialog, {
      width: '250px',
      data: { data: this.data, addPropertyDetails: this.addPropertyDetails }
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log(result)
      console.log('The dialog was closed');
      if (result) {

        console.log(this)
        if (this.cookie.get('userdata')) {
          thisModule.user = JSON.parse(this.cookie.get('userdata'));
          this.ownerEmail = thisModule.user.email;
          console.log(this.ownerEmail)
          if (thisModule.addPropertyDetails.listingType == 0) {
            thisModule.addMultipleProperties();
          } else {
            thisModule.addProperty();
          }
        }
        // this.tempUnitData.unitPrice = result.unitPrice;
        // this.tempUnitData.roomPrices = result.roomPrices;
      }
    });
    // $mdDialog.show({
    //     controller: 'DialogController',
    //     controllerAs: 'ctrl',
    //     templateUrl: '/views/dialog/availability-chart.html',
    //     parent: angular.element(document.body),
    //     clickOutsideToClose: true,
    //     fullscreen: true,
    //     locals: {
    //         parent: $scope
    //     }
    // }).then(function (response) {
    //     if (response == undefined) {
    //         window.scroll(0, 0);
    //     }
    // });
  };

  addProperty = function () {
    let thisModule = this;
    this.continueWithOutZillow();
    var data = {}, details = [], images = [], rent;
    this.authServiceService.authenticate(function (error, response) {
      // loaderService.showLoader("Adding Property");
      for (var i = 0; i < thisModule.tempUnitData.bedRooms; i++) {
        if (thisModule.tempUnitData.rentSplit == 0) {
          rent = thisModule.tempUnitData.unitPrice / thisModule.tempUnitData.bedRooms;
        } else {
          rent = thisModule.tempUnitData.roomPrices[i];
        }
        var is_occupied = false;
        if (thisModule.data.roomAvailability[0]) {
          if (thisModule.data.roomAvailability[0][0]) {
            if (thisModule.data.roomAvailability[0][0][i] === true) {
              is_occupied = thisModule.data.roomAvailability[0][0][i];
            }
          }
        }
        details.push({
          rent: rent,
          floor: thisModule.floorNo,
          id: 0,
          is_occupied: is_occupied,
          room_label: "",
          room: i + 1,
          soft_delete: false
        })
      }

      thisModule.tempUnitData.images.forEach(function (image) {
        images.push({
          isVideo: false,
          name: image,
          url: image
        })
      });
      data["amenities"] = thisModule.tempUnitData.amenities;
      data["city"] = thisModule.selectedProperty.getCity();
      data["current_occupancy"] = 0;
      data["distance"] = 0;
      data["favourites"] = 0;
      data["id"] = 0;
      data["total_occupancy"] = 0;
      data["latitude"] = thisModule.selectedProperty.getLatitude();
      data["longitude"] = thisModule.selectedProperty.getLongitude();
      data["market"] = thisModule.selectedProperty.getMarket();
      data["listing"] = true;
      data["media"] = images;
      data["bathrooms"] = thisModule.tempUnitData.bathRooms;
      data["bedrooms"] = thisModule.tempUnitData.bedRooms;
      data["myLike"] = false;
      data["details"] = details;
      data["name"] = thisModule.data.propertyName;
      data["title"] = thisModule.data.propertyName;
      data["description"] = thisModule.data.description;
      data["floor"] = 1;
      data["state"] = thisModule.selectedProperty.getState();
      data["street"] = thisModule.placeDetails.name;
      data["type"] = thisModule.addPropertyDetails.propertyType;
      data["zip"] = thisModule.selectedProperty.getZipCode();
      data["user_id"] = response.id;
      data["available_from"] = "2017-10-10";
      data["from_renter"] = thisModule.commonService.isRenterLogin();
      data["link_email"] = thisModule.ownerEmail;

      console.log(data)
      thisModule.ownerService.addProperty(data, function (error, details) {
        // loaderService.hideLoader();
        if (details && details.property_id) {
          thisModule.ownerService.savePropertyPartial(JSON.stringify({}), function (error, data) {
            thisModule.showAlertPropertyCreated();
          });
        } else {
          alert("error");
        }
      });
    })
  };

  addMultipleProperties = function () {
    let thisModule = this;
    this.continueWithOutZillow();
    var data = {};
    var units = [], images;
    var tempUnit;
    // loaderService.showLoader("Adding Properties");
    thisModule.data.units.forEach(function (unit, index) {
      tempUnit = {};
      var properties = [];

      tempUnit.name = unit.name;
      tempUnit.floor = 1;
      tempUnit.id = 0;
      tempUnit.no_of_rooms = unit.units;
      images = [];

      unit.images.forEach(function (image) {
        images.push({
          isVideo: false,
          name: image,
          url: image
        })
      });

      for (var i = 0; i < unit.units; i++) {
        var prop = {};
        var details = [];
        prop["amenities"] = unit.amenities;
        prop["area"] = 0;
        prop["id"] = 0;

        prop["available_from"] = "2017-10-10";
        prop["bathrooms"] = unit.bathRooms;
        prop["bedrooms"] = unit.bedRooms;
        prop["city"] = thisModule.selectedProperty.getCity();
        prop["total_occupancy"] = 0;
        prop["description"] = thisModule.selectedProperty.getDescription();
        prop["latitude"] = thisModule.selectedProperty.getLatitude();
        prop["longitude"] = thisModule.selectedProperty.getLongitude();
        prop["market"] = thisModule.selectedProperty.getMarket();
        prop["listing"] = true;
        prop["media"] = images;

        prop["myLike"] = false;
        if (thisModule.data.unitNames[index] !== undefined) {
          prop["title"] = thisModule.data.unitNames[index][i];
        }
        if (thisModule.data.unitFloors[index] !== undefined) {
          prop["floor"] = thisModule.data.unitFloors[index][i];
        }
        for (var j = 0; j < unit.bedRooms; j++) {
          var detail = {};

          console.log(unit.rentSplit, unit.unitPrice, unit.bedRooms)
          if (unit.rentSplit == 0) {
            detail["rent"] = unit.unitPrice / unit.bedRooms;
          } else {
            detail["rent"] = unit.roomPrices[j];
          }

          detail["floor"] = 1;

          if (thisModule.data.roomAvailability[index]) {
            if (thisModule.data.roomAvailability[index][i]) {
              if (thisModule.data.roomAvailability[index][i][j] === true) {
                detail["is_occupied"] = thisModule.data.roomAvailability[index][i][j];
              } else {
                detail["is_occupied"] = false;
              }
            } else {
              detail["is_occupied"] = false;
            }
          } else {
            detail["is_occupied"] = false;
          }
          detail["id"] = 0;
          detail["room"] = j + 1;
          detail["room_label"] = "Room " + detail["room"];

          details.push(detail);
        }
        prop["details"] = details;
        prop["state"] = thisModule.selectedProperty.getState();
        prop["street"] = thisModule.selectedProperty.getStreet();
        prop["zillow_rent"] = thisModule.selectedProperty.getZillowRent();
        prop["type"] = thisModule.addPropertyDetails.propertyType;
        prop["zip"] = thisModule.selectedProperty.getZipCode();
        prop["user_id"] = 0;
        prop["zpid"] = thisModule.selectedProperty.getZpid();
        prop["from_renter"] = thisModule.commonService.isRenterLogin();
        prop["link_email"] = thisModule.ownerEmail;

        properties.push(prop);
      }
      tempUnit.properties = properties;
      units.push(tempUnit);
    });
    data["city"] = thisModule.selectedProperty.getCity();
    data["latitude"] = thisModule.selectedProperty.getLatitude();
    data["longitude"] = thisModule.selectedProperty.getLongitude();
    data["name"] = thisModule.data.propertyName;
    data["description"] = thisModule.data.description;
    data["street"] = thisModule.selectedProperty.getStreet();
    data["state"] = thisModule.selectedProperty.getState();
    data["zip"] = thisModule.selectedProperty.getZipCode();
    data["units"] = units;

    console.log(data)
    thisModule.ownerService.addMultipleProperties(data, function (error, details) {

      // loaderService.hideLoader();
      if (details && details.status) {
        thisModule.ownerService.savePropertyPartial(JSON.stringify({}), function (error, data) {
          thisModule.showAlertPropertyCreated();
        })
      } else {
        alert("error");
      }
    })
  };

  continueWithOutZillow = function () {
    let mapModel = new MapModel();
    var property = new Property({}), lat, lng;
    if (typeof this.placeDetails.geometry.location.lat == "function") {
      lat = this.placeDetails.geometry.location.lat();
      lng = this.placeDetails.geometry.location.lng();
    } else {
      lat = this.placeDetails.geometry.location.lat;
      lng = this.placeDetails.geometry.location.lng;
    }
    property.setDescription("");
    property.setZillowRent("N/A");
    property.setLatitude(lat);
    property.setLongitude(lng);
    property.setCity(this.placeDetails.city);
    property.setState(this.placeDetails.state);
    property.setZipCode(this.placeDetails.zip);
    property.setZpid("");
    property.setMarket(0);
    property.setStreet(this.placeDetails.name)

    this.selectedProperty = property;

    this.data.newImagesCount = 0;
    this.data.thumbNails = [];
    this.data.newImages = [];

    // this.nextStep();
  };



}


