import { Component, OnInit, HostListener, OnDestroy, ViewChildren } from '@angular/core';
import { MatDialog, MatDialogConfig } from "@angular/material";
import { SignUpComponent } from './components/dialog/sign-up/sign-up.component'
import { CookieService } from 'ngx-cookie-service';
import { UserDataService, userDataTemplate } from './services/user-data.service'
import { Subscription } from 'rxjs';
import { RenterServiceService } from './services/renterService/renter-service.service'
import { Router } from '@angular/router';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})



export class AppComponent implements OnInit, OnDestroy {
  showSearch = false;
  showUserMenu = false;
  public windowWidth: any;
  user: userDataTemplate = new userDataTemplate();
  searchType = "university"
  authenticationStateSubscription: Subscription;
  showLoginSubscription: Subscription;

  constructor(private router: Router, private dialog: MatDialog, private userDataService: UserDataService, private cookie: CookieService, private renterService: RenterServiceService) {
    this.authenticationStateSubscription = userDataService.authenticationState$.subscribe(user => {
      this.user = user;
    })

    this.showLoginSubscription = userDataService.showLogin$.subscribe(() => {
      // this.user = user;
      this.showLoginDialog(false);
    })
  }
  selectedCity
  formattedAddress
  setAddress($event) {
    this.selectedCity = $event.vicinity;
    this.formattedAddress = $event.formatted_address;

    this.search();
  }

  search() {
    let queryParams = {}
    if (this.selectedCity != "" && this.searchType == 'city') {
      // console.log("test", this.selectedCity)
      queryParams["sc"] = this.selectedCity;
      queryParams["csv"] = this.selectedCity;
    }

    if (this.searchType == 'zipcode') {
      queryParams["zc"] = this.searchValue;
    }

    this.showSearch = false;
    this.router.navigate(["search-property"], { queryParams: queryParams })
  }
  resetSuggestions() {
    this.suggestions = [];
  };


  @ViewChildren('searchCity') searchCity;
  @ViewChildren('searchG') searchG;
  changeSearchType(ev) {
    console.log(ev)

    setTimeout(() => {
      if (ev.value == "city") {
        this.searchCity.first.nativeElement.focus();
      } else {
        this.searchG.first.nativeElement.focus();

      }
    }, 50)

    this.resetSuggestions();
  };

  ngOnInit() {
    this.windowWidth = window.innerWidth;
    let userdata = this.cookie.get("userdata");
    try {
      this.user = JSON.parse(this.cookie.get("userdata"));
    } catch{
      this.user = null;
    }
    // setTimeout(()=>{
    // 
    // }, 1000)
    this.userDataService.announceauthenticationStateChange(this.user);
  }

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.windowWidth = window.innerWidth;
  }

  showLoginDialog(isLogin) {
    const dialogConfig = new MatDialogConfig();

    dialogConfig.disableClose = false;
    dialogConfig.autoFocus = true;
    dialogConfig.data = {
      isLogin: isLogin
    };

    this.dialog.open(SignUpComponent, dialogConfig);
  }

  redirectToListing(value) {
    this.showSearch = false;
    this.router.navigate(["search-property"], { queryParams: { loc: value.name, sid: value.id } })
  };

  suggestions = []
  searchValue = "";

  updateSuggestions(ev) {

    if (this.searchType != "university") {
      return
    }
    let thisModule = this;
    thisModule.suggestions = [];
    this.renterService.getSearchSuggestions(this.searchValue, function (error, data) {
      if (data) {
        console.log("Updated")
        thisModule.suggestions = data.universities;
      }
    });
  };

  focusOut = false;

  searchFocusIn() {
    this.focusOut = false;
  }

  searchFocusOut(ev) {
    // console.log(ev.relatedTarget.id)

    if (ev.relatedTarget && ev.relatedTarget.id == "searchBy") {
      return
    }
    setTimeout(() => {
      this.focusOut = true;
      this.showSearch = false;
    }, 1000)
    // this.suggestions = [];
    // this.searchValue = ""
  }

  logout() {
    this.userDataService.announceauthenticationStateChange(new userDataTemplate());
    this.showUserMenu = false;

    this.router.navigate([""]);
  }

  ngOnDestroy() {
    this.authenticationStateSubscription.unsubscribe();
    this.showLoginSubscription.unsubscribe();
  }

  setSearch = function (value) {
    if (value) {
      this.showSearch = value;
      // $timeout(function () {
      setTimeout(() => {
        document.getElementById("propertySearchString").focus();
      }, 100)
      // }, 100);
    } else {
      // $timeout(function () {
      this.showSearch = value;
      // }, 100);
    }
  }


}
