import { Component, OnInit } from '@angular/core';
import { RenterServiceService } from '../../../services/renterService/renter-service.service'
import { CommonService } from '../../../services/common/common.service'
import { DetailsService } from './../../../services/details/details.service'
import { DetailsRoomService } from './../../../services/details/details-room.service'
import { ActivatedRoute } from '@angular/router';
import { default as Keys } from './../../../utils/Keys'
import { OwnerService } from '../../../services/owner/owner.service';
import { MatDialogRef, MatDialog, MAT_DIALOG_DATA } from '@angular/material'
import { AdvancedPropertyEditDialog } from './../../dialog/advanced-property-edit/advanced-property-edit'
import { UploadEvent, UploadFile, FileSystemFileEntry, FileSystemDirectoryEntry } from 'ngx-file-drop';
import { FileUploadHelper } from './../../../utils/FileUploadHelper'

declare var google;
declare var $;
@Component({
  selector: 'app-edit-property',
  templateUrl: './edit-property.component.html',
  styleUrls: ['./edit-property.component.css']
})
export class EditPropertyComponent implements OnInit {

  amenities: any = [];
  details: any = {};
  id = this.location.snapshot.queryParamMap.get("id")
  // ref = $location.search()["ref"];
  distance: any = {};
  checkedRooms: any = {};
  options: any = {};
  topic: any = {};
  isEdit: any = false;
  s3Url: any = 'https://propertyowneruploads.s3.amazonaws.com/propertyImages%2F';
  s3Details: any = {};
  roomDetails: any = [];
  roomDetailsCopy: any = [];
  directionsDisplay: any;
  directionsService: any = new google.maps.DirectionsService();
  map: any;
  propertyType: any;
  bannerImage: any;
  accountForProperty: any
  constructor(
    public dialog: MatDialog,
    private commonService: CommonService,
    private renterService: RenterServiceService,
    private detailsService: DetailsService,
    private detailsRoomService: DetailsRoomService,
    private location: ActivatedRoute,
    private ownerService: OwnerService
  ) { }

  ngOnInit() {
    let thisModule = this;
    this.getS3Details(null, null, null);

    this.renterService.getAmenities((err, data) => {
      if (err) {
        console.log("err", err)
      } else {
        thisModule.amenities = data.results
      }
    })


    this.detailsService.get(this.id, function (error1, data1) {
      // loaderService.hideLoader();
      if (!error1) {
        console.log(data1)
        thisModule.details = data1;
        thisModule.details.thumbNails = JSON.parse(JSON.stringify(thisModule.details.media));

        console.log(thisModule.details)
        thisModule.roomDetails = thisModule.details.details;
        thisModule.roomDetailsCopy = thisModule.details.details.slice(0);

        thisModule.details.topicId = data1.id;
        thisModule.propertyType = Keys.PROPERTY_TYPE[data1.type];
        if (data1.media[0]) {
          thisModule.bannerImage = data1.media[0].url;
        }

        thisModule.options.ownerUserId = data1.owner.user.email;
        thisModule.options.ownerDisplayName = data1.owner.user.first_name;
        thisModule.options.ownerImage = data1.owner.user.profile_pic;

        thisModule.topic.title = data1.street;
        thisModule.topic.subTitle = data1.city + " " + data1.state;
        thisModule.topic.key1 = "$";
        thisModule.topic.value1 = data1.extra_details.rent;
        thisModule.topic.value2 = data1.extra_details.bed + " bath:" + data1.extra_details.bath;
        thisModule.topic.key2 = "bed:";
        if (data1.media[0]) {
          thisModule.topic.imageLink = data1.media[0].url;
        }
        thisModule.updateSelectAmenitiesList(data1.amenities);
        thisModule.options.topic = thisModule.topic;;
      }

    });

    this.detailsRoomService.get(this.id, function (error1, data1) {
      console.log(error1)
      if (!error1) {
        data1.results.forEach(function (e) {
          if (typeof e.rent === "string") {
            e.rent = parseFloat(e.rent);
          }
        });
        thisModule.roomDetails = data1.results;
        thisModule.roomDetailsCopy = data1.results.slice(0);
        console.log(thisModule.roomDetailsCopy)
      }

    });

    this.getAccountForProperty();
  }

  getS3Details(callback, callbackData, callbackS3) {
    let thisModule = this;
    this.commonService.getS3Details(function (error, data) {
      console.log(data)
      if (!error) {
        thisModule.s3Details = data;
        if (callbackS3) {
          callbackS3(null, data)
        }
        if (callback) {
          callback(callbackData);
        }
      } else {
        if (callbackS3) {
          callbackS3(error, null)
        }
      }
    })
  };


  updateSelectAmenitiesList(list) {
    let thisModule = this;
    list.forEach(function (amenity) {
      for (let iter in thisModule.amenities) {
        if (thisModule.amenities[iter].id == amenity.id) {
          thisModule.amenities[iter].selected = true;
        }
      }
    });
  };

  updateSelectedAmenities(amenity, value) {
    if (!value) {
      this.details.amenities.splice(this.details.amenities.indexOf(amenity), 1);
    } else {
      this.details.amenities.push(amenity);
    }
    amenity.selected = value;
  };


  removeImage(id) {
    this.details.thumbNails.splice(id, 1);
    this.details.media.splice(id, 1);
  };

  addRoom() {
    this.roomDetailsCopy.push({
      'id': 0,
      'is_occupied': false,
      'rent': 1,
      'room_label': " Room " + (this.roomDetailsCopy.length + 1)
    });
  };

  deleteRoom(index) {
    this.roomDetailsCopy.splice(index, 1);
  };
  unAssignRoom(index) {
    var data = {
      "property_renter_id": this.roomDetailsCopy[index].property_renter_id,
      "status": 4
    };
    // loaderService.showLoader(" Unassigning room ");
    this.ownerService.editPropertyRenterService(data, function (error1, data1) {
      // loaderService.hideLoader();
      if (!error1) {
        this.roomDetailsCopy[index].renter_first_name = null;
        this.roomDetailsCopy[index].renter_last_name = null;
        this.roomDetailsCopy[index].is_occupied = false;
        this.roomDetails[index] = this.roomDetailsCopy[index];
      }
    });
  };
  errorText = null;

  saveProperty = function () {
    // loaderService.showLoader(" Saving Property details");
    this.ownerService.editPropertyService(this.details, function (error1, data1) {
      // loaderService.hideLoader();
      window.location.replace('/spaces');
    });
  };
  scrollTo = function (id) {
    // $('html, body').animate({
    // scrollTop: $("#" + id).offset().top
    // }, 500);
  };
  // this.$watch('files-single', function () {
  // this.upload(this.files);
  // });
  // this.$watch('file', function () {
  // // if (this.file != null) {
  // // this.files = [this.file];
  // // }
  // });
  setDistance = function (data) {
    this.distance[data.request.travelMode] = data.routes[0].legs[0].duration.text;
    this.$apply();
  };

  openModal = function () {
    document.getElementById('myModal').style.display = "block";
  };

  closeModal = function () {
    document.getElementById('myModal').style.display = "none";
  };

  slideIndex = 1;

  resetSelectedRooms = function () {
    this.checkedRooms = {};
  };

  plusSlides = function (n) {
    this.showSlides(this.slideIndex += n);
  };

  currentSlide = function (n) {
    this.showSlides(this.slideIndex = n);
  };

  showSlides = function (n) {
    var i;
    var slides = document.getElementsByClassName("mySlides");
    if (n > slides.length) {
      this.slideIndex = 1
    }
    if (n < 1) {
      this.slideIndex = slides.length
    }
    for (i = 0; i < slides.length; i++) {
      // slides[i].style.display = "none";
    }
    // slides[this.slideIndex - 1].style.display = "block";
  };

  // this.$on('event:auth', function(event, data){
  // this.authenticated = data.authenticated;
  // if (data.details) {
  // this.options.userId = data.details.id;
  // this.options.userName = data.details.first_name;
  // this.options.email = data.details.email;
  // }
  // Chat.init(this.options);
  // this.$apply();
  // });

  getAccountForProperty() {
    let thisModule = this;
    this.ownerService.getAccountForProperty({ property_id: this.id }, function (error, data) {
      if (!error) {
        console.log(data)
        thisModule.accountForProperty = data;
        // $timeout(function () {
        // this.$apply();
        // }, 0);
      }
    })
  };


  public fileOver(event) {
    console.log(event);
  }

  public fileLeave(event) {
    console.log(event);
  }

  public selectedFiles(event) {
    this.upload(event.files, true)
  }
  uploadedImages: any;
  public dropped(event: UploadEvent) {
    console.log("File Dropped");

    this.uploadedImages = event.files;
    for (const droppedFile of event.files) {

      // Is it a file?
      if (droppedFile.fileEntry.isFile) {
        const fileEntry = droppedFile.fileEntry as FileSystemFileEntry;
        fileEntry.file((file: File) => {

          // Here you can access the real file
          console.log(droppedFile.relativePath, file);
          this.upload(file, false)

        });
      } else {
        // It was a directory (empty directories are added, otherwise only files)
        const fileEntry = droppedFile.fileEntry as FileSystemDirectoryEntry;
        console.log(droppedFile.relativePath, fileEntry);
      }
    }
  }

  oldDropFiles: any;
  upload(files, buttonUpload) {
    if (!Array.isArray(files)) {
      files = [files]
    }

    let thisModule = this;
    if (!thisModule.oldDropFiles) {
      thisModule.oldDropFiles = files;
    } else {
      if (thisModule.oldDropFiles == files) {
        return;
      } else {
        thisModule.oldDropFiles = files;
      }
    }
    if (files && files.length) {

      this.details.newImagesCount = this.details.thumbNails.length;
      $.each(files, function (_, value) {
        console.log(_, value)
        var reader = new FileReader();
        reader.onload = function (e) {
          console.log(e)
          var options = {
            file: "",
            signature: thisModule.s3Details.SIGNATURE,
            s3_url: "https://propertyowneruploads.s3.amazonaws.com/",
            policy: thisModule.s3Details.POLICY,
            path: "propertyImages/",
            index: null
          };
          thisModule.details.thumbNails.push({
            index: thisModule.details.newImagesCount,
            name: e.target["result"],
            src: e.target["result"],
            isUpload: true
          });

          if (buttonUpload) {
            options.file = value[0];
          } else {
            options.file = value;
          }
          options.index = thisModule.details.newImagesCount;
          var helper = new FileUploadHelper(options);
          helper.upload(function (imageId) {
            thisModule.details.media.push({
              'id': helper.options.index,
              'name': thisModule.s3Details.S3_URL + "propertyowneruploads/propertyImages/" + imageId,
              'url': thisModule.s3Details.S3_URL + "propertyowneruploads/propertyImages/" + imageId,
              'type': thisModule.s3Details.S3_URL + "propertyowneruploads/propertyImages/" + imageId
            });
          });
          // helper.upload(function (imageId) {
          //   if (thisModule.details.thumbNails[helper.options.index]) {
          //     console.log(thisModule.s3Details);
          //     helper.upload(function (imageId) {
          //       thisModule.details.media.push({
          //         'id': helper.options.index,
          //         'name': thisModule.s3Details.S3_URL + imageId,
          //         'url': thisModule.s3Details.S3_URL + imageId,
          //         'type': thisModule.s3Details.S3_URL + imageId
          //       });
          //     });
          //     // thisModule.details.media.push(thisModule.s3Details.S3_URL + "propertyowneruploads/propertyImages/" + imageId);
          //     console.log(thisModule.details.images);
          //   }
          // });
          thisModule.details.newImagesCount++;
        };

        if (buttonUpload) {
          reader.readAsDataURL(value[0]);
          // options.file = value[0];
        } else {
          reader.readAsDataURL(value);
        }
      });
    }
  };

  openDialog() {
    const dialogRef = this.dialog.open(AdvancedPropertyEditDialog, {
      width: '100vw',
      height: '100vh',
      data: { roomDetailsCopy: this.roomDetailsCopy },

    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed', result);
      if (result) {
        this.roomDetailsCopy = result;
        console.log(this.roomDetailsCopy);
        for (var i = 0; i < this.roomDetailsCopy.length; i++) {
          if (this.roomDetailsCopy[i].rent <= 0 || this.roomDetailsCopy[i].rent == undefined) {
            this.errorText = "Invalid room rent ";
            return;
          }
        }
        this.errorText = null;
        // $mdDialog.hide();
        // loaderService.showLoader(" Saving room details");
        this.ownerService.editRoomPropertyService({
          "details": this.roomDetailsCopy
        }, function (error1, data1) {
          // loaderService.hideLoader();

          if (!error1) {
            window.location.reload();
          }
        });
      }
    });
  }
}
