
import { Component, Inject, OnInit } from "@angular/core"
import { MatDialogRef, MAT_DIALOG_DATA } from "@angular/material"

import { OwnerService } from "../../../services/owner/owner.service";
import { FormBuilder } from "@angular/forms";

@Component({
    selector: 'add-card',
    templateUrl: './add-card.html'
})
export class AddCardDialog implements OnInit {
    cardDetails: any;
    constructor(
        public ownerService: OwnerService,
        public dialogRef: MatDialogRef<AddCardDialog>,
        @Inject(MAT_DIALOG_DATA) data: any,
    ) {
        this.cardDetails = data.cardDetails
    }

    ngOnInit() {
    }

    closeDialog(answer): void {
        console.log(this.cardDetails)
        if (answer) {
            this.dialogRef.close({ status: answer, cardDetails: this.cardDetails });
        } else {
            this.dialogRef.close();
        }
    }
}
