import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Config } from "../../../utils/Config";
import { OwnerService } from "../../../services/owner/owner.service"
import { DatePipe } from '@angular/common'
import { ContractForm } from '../../../models/CreateContractForm'
import { MatDialog } from '@angular/material'
import { AddCardDialog } from './../../dialog/add-card/add-card'
import { MoveInConfirmationDialog } from './../../dialog/move-in-confirmation/move-in-confirmation'
import { PayTokenDialog } from './../../dialog/pay-token/pay-token'
import { OwnerSendContractDialog } from './../../dialog/owner-send-contract/owner-send-contract'
import { OwnerSignContractDialog } from './../../dialog/owner-sign-contarct/owner-send-contract'
import { UserDataService } from '../../../services/user-data.service'
import { Subscription } from 'rxjs';

import * as Quill from 'quill';
import { DownloadContractDialog } from '../../dialog/download-contract/download-contract.component';
import { RenterServiceService } from '../../../services/renterService/renter-service.service';

let thisModule;
@Component({
  selector: 'app-reports',
  templateUrl: './reports.component.html',
  styleUrls: ['./reports.component.css']
})
export class ReportsComponent implements OnInit, OnDestroy {
  authenticationStateSubscription: Subscription
  stripeCardAdded: boolean;
  constructor(
    private location: ActivatedRoute,
    private ownerService: OwnerService,
    public datepipe: DatePipe,
    public dialog: MatDialog,
    private userDataService: UserDataService,
    private renterService: RenterServiceService,
    private router: Router

  ) {
    thisModule = this;
    this.authenticationStateSubscription = this.userDataService.authenticationState$.subscribe(user => {
      if (user) {
        console.log(user)
        this.fetchReports(1);
      } else {
        this.properties = [];
        this.contracts = [];
      }
    })
  }

  ngOnInit() {
    setTimeout(() => {
      this.fetchReports(1)
    }, 1000)
    this.ownerService.checkOwnerStripeCreds().subscribe((res: { message: boolean }) => {
      if (res && res.message == false) {
        this.addCardDialog();
      }
    }, error => {
      console.log(error);
	});
	this.ownerService.getOwnerNotifications().subscribe((res) => {
		console.log(res);
	  }, error => {
		console.log(error);
	  });
  }

  ConfigObj = Config()
  ref = this.location.snapshot.queryParamMap.get("ref");
  itemsPerPage = this.ConfigObj["ITEMS_PER_PAGE"];
  currentReport = undefined;
  contractForm = ContractForm({});
  currentProperty = undefined;
  filters = { temp_status: -1, status: null, start_date: null, end_date: null };
  dates = { start: null, end: null };
  TYPE_CHOICES = [
    "move in request by renter",
    "Confirmed",
    "Declined",
    "Blocked",
    "Moved out",
    "Contract to be filled",
    "Owner Signed",
    "Contract to be signed",
    "email awaiting approval",
    "email approval declined",
    "Background check requested",
    "Background details filled by renter",
    "Background details initiated by owner",
    "Background check complete",
    "Contract to be signed by renter"
  ];

  statusMap = {
    0: "Lease requested by renter",
    1: "Confirmed",
    2: "Lease request declined",
    3: "Blocked",
    4: "Moved out",
    5: "Owner initiated",
    6: "Owner Signed",
    7: "Document signed by renter",
    8: "email awaiting approval",
    9: "email approval declined",
    10: "Background check requested",
    11: "Background details filled by renter",
    12: "Background details initiated by owner",
    13: "Background check complete",
    14: "Contract shared by owner",
    15: "Invalid",
    16: "Contract signed by owner"
  };
  actionMap = {
    0: "Accept/Reject",
    5: "Send Lease Agreement"
  };
  nonActionMap = {
    1: "Confirmed",
    2: "Declined",
    3: "Blocked",
    4: "Moved out",
    6: "Owner Signed",
    8: "email awaiting approval",
    9: "email approval declined",
    10: "Background check requested",
    11: "Background details filled by renter",
    12: "Background details initiated by owner",
    13: "Background check complete",
    14: "Waiting for renter to sign contract",
    7: "Document signed by renter"
  };
  quill = null;
  contracts = [];
  cards = null;
  selectedCardId = null;
  cardDetails = {
    number: null,
    exp_month: null,
    exp_year: null,
    cvc: null,
  };
  properties = [];
  reportsTotal: null;
  updateFilters() {
    if (this.dates.start) {
      this.filters.start_date = this.datepipe.transform(this.dates.start, 'yyyy-MM-dd');
    }

    if (this.dates.end) {
      this.filters.end_date = this.datepipe.transform(this.dates.end, 'yyyy-MM-dd');
    } else if (this.dates.start) {
      this.filters.end_date = this.datepipe.transform(new Date(), 'yyyy-MM-dd');
    }

    // if (this.filters.status == "") {
    // delete this.filters["status"];
    // }

    // this.filters["status"] = this.filters["status"].toString();

    this.fetchReports(1);
  };

  acceptOrRejectRequest = function (status) {
    var requestData = {
      property_renter_id: "" + this.currentReport.id,
      status: "" + status
    };

    let user = this.userDataService.user;

    this.ownerService.updatePropertyRenterStatus(requestData, function (error, data) {
      // console.log("")
      if (!error) {
        thisModule.currentReport.status = status;
        if (status === 5) {
          thisModule.showDialogue(thisModule.currentReport);
        }
      }
    });
  };

  getReportData(propertyRenterId, callback) {
    // loaderService.showLoader(" Fetching Details ");
    this.renterService.getPDFData(propertyRenterId, function (error, data) {
      // this.loaderService.hideLoader();
      if (!error) {
        // this.$apply();
        callback(data);
      }
    });
  };

  openDownloadDialog(report) {
    this.getReportData(report.id, function (data) {
      const dialogRef = thisModule.dialog.open(DownloadContractDialog, {
        width: '100vw',
        height: '100vh',
        data: { test: "test", contractForm: data, isOwner: true, status: report.status },

      });

      dialogRef.afterClosed().subscribe(result => {
        if (result) {

        }
      })
    });

  }

  moveInConfirmation(report) {
    const dialogRef = this.dialog.open(MoveInConfirmationDialog, {
      width: '100vw',
      height: '100vh',
      data: { test: "test", report: report },

    });

    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.acceptOrRejectRequest(result);
      }
    })

    //     $mdDialog.show({
    //         controller: 'DialogController',
    //         controllerAs: 'ctrl',
    //         templateUrl: '/views/dialog/move-in-confirmation.html',
    //         parent: angular.element(document.body),
    //         clickOutsideToClose: true,
    //         fullscreen: true,
    //         locals: {report: report}
    //     }).then(function (response) {
    //         this.acceptOrRejectRequest(response);
    //     }, function () {
    //     });
  };
  currentTab;
  fillContractForm() {
    this.currentTab = 0;

    const dialogRef = this.dialog.open(OwnerSendContractDialog, {
      width: '100vw',
      height: '100vh',
      data: { contractForm: this.contractForm, currentTab: this.currentTab },

    });

    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.quill = result;
        this.createContract();
      }
    })
    //     $mdDialog.show({
    //         controller: 'DialogController',
    //         controllerAs: 'ctrl',
    //         templateUrl: '/views/dialog/owner-send-contract.html',
    //         parent: angular.element(document.body),
    //         clickOutsideToClose: true,
    //         fullscreen: true,
    //         locals: {contractForm: this.contractForm, currentTab: this.currentTab, parent: $scope}
    //     }).then(function (response) {
    //         if (response == "success") {
    //             this.createContract();
    //         }
    //     }, function () {

    //     });
  };

  signContractDialog(renter_name) {
    this.currentTab = 0;

    const dialogRef = this.dialog.open(OwnerSignContractDialog, {
      width: '100vw',
      height: '100vh',
      data: { contractForm: this.contractForm, currentTab: this.currentTab, renter_name: renter_name },

    });

    dialogRef.afterClosed().subscribe(result => {
      if (result == "5") {
        this.currentReport.status = 16;
        // this.getCards();
      }
    })
    //     $mdDialog.show({
    //         controller: 'DialogController',
    //         controllerAs: 'ctrl',
    //         templateUrl: '/views/dialog/owner-sign-contarct.html',
    //         parent: angular.element(document.body),
    //         clickOutsideToClose: true,
    //         fullscreen: true,
    //         locals: {contractForm: this.contractForm, currentTab: this.currentTab, parent: $scope}
    //     }).then(function (response) {
    //         if (response == "5") {
    //             this.currentReport.status = 16;
    //             this.getCards();
    //         }
    //     }, function () {

    //     });
  };

  addCardDialog() {

    const dialogRef = this.dialog.open(AddCardDialog, {
      width: '100vw',
      height: '100vh',
      data: { test: "test", cardDetails: this.cardDetails },

    });

    dialogRef.afterClosed().subscribe((result) => {
      if (result && result.status == "add") {
        this.cardDetails = result.cardDetails
        this.addCard();
        this.cardDetails = {
          number: null,
          exp_month: null,
          exp_year: null,
          cvc: null,
        };
      } else {
        this.showDialogue(this.currentReport);
        this.cardDetails = {
          number: null,
          exp_month: null,
          exp_year: null,
          cvc: null,
        };;
      }
    })
    //     $mdDialog.show({
    //         controller: 'DialogController',
    //         controllerAs: 'ctrl',
    //         templateUrl: '/views/dialog/add-card.html',
    //         parent: angular.element(document.body),
    //         clickOutsideToClose: true,
    //         fullscreen: true,
    //         locals: {parent: $scope}
    //     }).then(function (response) {
    //         if (response == "add") {
    //             this.addCard();
    //             this.cardDetails = {};
    //         } else {
    //             this.showDialogue(this.currentReport);
    //             this.cardDetails = {};
    //         }
    //     }, function () {
    //         this.cardDetails = {};
    //     });
  };

  addCardMode;
  payTokenDialog() {
    this.addCardMode = false;
    const dialogRef = this.dialog.open(PayTokenDialog, {
      width: '100vw',
      height: '100vh',
      data: { test: "test", selectedCardId: this.selectedCardId, cards: this.cards },

    });

    dialogRef.afterClosed().subscribe(result => {
      if (result && result.status == "pay") {
        this.selectedCardId = result.selectedCardId
        this.payToken();
      } else {
        this.addCardDialog();
      }
    })

    //     $mdDialog.show({
    //         controller: 'DialogController',
    //         controllerAs: 'ctrl',
    //         templateUrl: '/views/dialog/pay-token.html',
    //         parent: angular.element(document.body),
    //         clickOutsideToClose: true,
    //         fullscreen: true,
    //         locals: {parent: $scope}
    //     }).then(function (response) {
    //         if (response == "pay") {
    //             this.payToken();
    //         } else {
    //             this.addCardDialog();
    //         }

    //     }, function () {

    //     });
  };


  showDialogue(report) {
    thisModule.currentReport = report;

    var ownerName, renterName;

    if (report.status === 0) {
      thisModule.moveInConfirmation(report);
    } else if (report.status === 5) {
      // console.log()
      // ownerName = thisModule.userDetails.first_name;
      renterName = report.renter_name;

      // if (thisModule.userDetails.last_name) {
      // ownerName = ownerName + " " + thisModule.userDetails.last_name;
      // }

      thisModule.contractForm.property_renter_id = report.id;
      thisModule.contractForm.owner_name = thisModule.userDataService.user.name;
      thisModule.contractForm.type = 1;
      thisModule.contractForm.renter_name = renterName;
      thisModule.contractForm.rooms = report.details.length;
      thisModule.contractForm.city = report.property_city;
      thisModule.contractForm.state = report.property_state;
      thisModule.contractForm.zip = report.property_zip;
      thisModule.contractForm.address = report.property_street;
      thisModule.contractForm.property_id = thisModule.property_id;
      setTimeout(() => {
        thisModule.fillContractForm();
      }, 500)
    } else if (report.status === 7) {
      setTimeout(() => {
        thisModule.signContractDialog(renterName);
      }, 500)
    } else if (report.status === 14) {
      setTimeout(() => {
        thisModule.getCards();
      }, 500)

    }

    console.log(report.status)
  };

  createContract = function () {
    thisModule.contractForm.quill = thisModule.quill.getContents();
    thisModule.ownerService.createContract(thisModule.contractForm, function (error, data) {
      if (!error) {
        thisModule.contractForm = {};
        thisModule.currentTab = 0;
        thisModule.contracts.splice(thisModule.contracts.indexOf(thisModule.currentReport), 1);
      }
    });
  };

  getPropertyById(id) {
    var prop;

    this.properties.forEach(function (property) {
      if (property.id == id) {
        prop = property;
      }
    });

    return prop;
  };

  updateSelectedCard(data) {
    this.selectedCardId = data.id;
  };

  signContract() {
    this.ownerService.checkPaidForContract(this.currentReport.id, function (error, data) {
      if (data && data.status) {
        this.renterService.getPDFData(this.currentReport.id, function (error, data) {
          if (!error) {
            if (!this.staticQuill) {
              this.staticQuill = new Quill('#editor-container-static', {
                modules: {
                  toolbar: []
                },
                placeholder: 'Compose an epic...',
                theme: 'snow'  // or 'bubble'
              });
            }
            this.staticQuill.setContents(data.data.quill);
            this.staticQuill.disable();
            var reqData = {
              property_renter_id: this.currentReport.id,
              quill: this.staticQuill.container.firstChild.innerHTML
            };
            this.ownerService.signContract(reqData, function (error, data) {
              if (!error) {
                window.location.reload();
              }
            });
          }
        });
      } else {
        this.getCards();
      }
    });
  };

  getCards() {
    this.ownerService.getOwnerCards(function (error, data) {
      thisModule.cards = data.results;
      // this.$apply();
      thisModule.payTokenDialog();
    });
  };

  addCard() {
    // loaderService.showLoader("Adding  Card Details");
    this.ownerService.addCard(this.cardDetails, function (error, data) {
      // loaderService.hideLoader();
      if (error) {
        alert("Invalid card Details");
      } else {
        this.getCards();
      }
    });
  };

  payToken() {
    if (this.selectedCardId !== undefined) {
      var requestData = {
        property_renter_id: "" + this.currentReport.id,
        card_id: "" + this.selectedCardId
      };

      this.ownerService.payToken(requestData, function (error, data) {
        if (!error) {
          this.signContract();
        }
      });
    }
  };

  next = null;

  pageChanged(pageNumber) {
    this.fetchReports(pageNumber);
  };

  fetchReports(page) {

    if (this.userDataService.user.isRenter) {
      return this.router.navigate([""])
    }
    if (this.filters["temp_status"] == -1) {
      delete this.filters["status"];
    } else {
      this.filters["status"] = this.filters["temp_status"];
    }

    let user = this.userDataService.user;
    let thisModule = this;

    // if (user.isOwner) {
    this.ownerService.getContracts(page, this.filters, function (error, data) {
      if (!error) {
        thisModule.contracts = data.results;
        thisModule.reportsTotal = data.count;
      }
    });
    // } else if (user.isRenter) {
    // this.renterService.getContracts(page, this.filters, function (error, data) {
    // if (!error) {
    // thisModule.contracts = data.results;
    // thisModule.reportsTotal = data.count;
    // }
    // });
    // }
  };

  ngOnDestroy() {
    this.authenticationStateSubscription.unsubscribe();
  }

}
