import { Component, OnInit, HostListener } from '@angular/core';
import { UserDataService, userDataTemplate } from '../../../services/user-data.service'
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  showRenter = true;
  showVideo = true;
  loadCarousel1 = false;
  user: userDataTemplate = new userDataTemplate();
  currentSlide: number = 1;
  slideConfig = { "slidesToShow": 3, "slidesToScroll": 3 };
  slides = [
    {
      img: "/assets/images/room1.jpeg",
      bath: 1,
      bed: 2,
      line1: "albuquerque, NM 87121",
      line2: "609 Vista Luna Lane",
      id: 618,
      price: 700,
      payFrequency: "month"
    },
    {
      img: "/assets/images/room2.jpeg",
      bath: 2,
      bed: 3,
      line1: "albuquerque, NM 87121",
      line2: "609 Vista Luna Lane",
      id: 619,
      price: 700,
      payFrequency: "month"
    },
    {
      img: "/assets/images/room3.jpeg",
      bath: 1,
      bed: 2,
      line1: "albuquerque, NM 87121",
      line2: "609 Vista Luna Lane",
      id: 617,
      price: 700,
      payFrequency: "month"
    },
    {
      img: "/assets/images/room4.png",
      bath: 1,
      bed: 2,
      line1: "albuquerque, NM 87121",
      line2: "609 Vista Luna Lane",
      id: 616,
      price: 700,
      payFrequency: "month"
    },
    {
      img: "/assets/images/room2.jpeg",
      bath: 1,
      bed: 2,
      line1: "albuquerque, NM 87121",
      line2: "609 Vista Luna Lane",
      id: 615,
      price: 700,
      payFrequency: "month"
    },
    {
      img: "/assets/images/room1.jpeg",
      bath: 1,
      bed: 2,
      line1: "albuquerque, NM 87121",
      line2: "609 Vista Luna Lane",
      id: 614,
      price: 700,
      payFrequency: "month"
    },
    {
      img: "/assets/images/room3.jpeg",
      bath: 1,
      bed: 2,
      line1: "albuquerque, NM 87121",
      line2: "609 Vista Luna Lane",
      id: 613,
      price: 700,
      payFrequency: "month"
    }
  ];

  authenticationStateSubscription: Subscription;

  constructor(private userDataService: UserDataService) {
    this.authenticationStateSubscription = userDataService.authenticationState$.subscribe(user => {
      this.user = user;
    })
  }

  windowWidth

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.windowWidth = window.innerWidth;
    if (this.windowWidth < 768) {
      this.slideConfig = { "slidesToShow": 1, "slidesToScroll": 1 }
    } else if (this.windowWidth < 960) {
      this.slideConfig = { "slidesToShow": 2, "slidesToScroll": 2 }
    } else {
      this.slideConfig = { "slidesToShow": 3, "slidesToScroll": 3 }
    }
  }

  ngOnInit() {
    this.windowWidth = window.innerWidth;
  }

  showRenterData(val) {
    this.showRenter = val;
  }


  ngOnDestroy() {
    this.authenticationStateSubscription.unsubscribe();
  }


}
