import { Component, OnInit } from '@angular/core';
import { DatePipe } from '@angular/common'
import * as Quill from 'quill';
import { RenterServiceService } from '../../../services/renterService/renter-service.service';
import { Config } from "../../../utils/Config";
import { RenterSignContractDialog } from './../../dialog/renter-sign-contract/renter-sign-contract'
import { MatDialog } from '@angular/material'

import { DownloadContractDialog } from '../../dialog/download-contract/download-contract.component'
import { UserDataService } from './../../../services/user-data.service'
import { Router } from '@angular/router';
let thisModule;
@Component({
  selector: 'app-renter-report',
  templateUrl: './renter-report.component.html',
  styleUrls: ['./renter-report.component.css']
})
export class RenterReportComponent implements OnInit {
  filters = { temp_status: -1, status: null, start_date: null, end_date: null };
  reports = [];
  dates = { start: null, end: null };

  constructor(
    public datepipe: DatePipe,
    private renterService: RenterServiceService,
    public dialog: MatDialog,
    public userDataService: UserDataService,
    private router: Router
  ) {
    thisModule = this;
  }

  ngOnInit() {
    setTimeout(() => {
      this.fetchReports(1)
    }, 1000)
  }

  updateFilters() {
    if (this.dates.start) {
      this.filters.start_date = this.datepipe.transform(this.dates.start, 'yyyy-MM-dd');
    }

    if (this.dates.end) {
      this.filters.end_date = this.datepipe.transform(this.dates.end, 'yyyy-MM-dd');
    } else if (this.dates.start) {
      this.filters.end_date = this.datepipe.transform(new Date(), 'yyyy-MM-dd');
    }

    if (this.filters.status == "") {
      delete this.filters["status"];
    }
    this.fetchReports(1);
  };

  reportData = undefined;
  quill = null;
  itemsPerPage = Config["ITEMS_PER_PAGE"];

  statusMap = {
    0: "Awaiting Response",
    1: "Confirmed",
    2: "Declined",
    3: "Blocked",
    4: "Moved out",
    5: "Owner initiated",
    6: "Owner Signed",
    7: "Document signed by renter",
    8: "email awaiting approval",
    9: "email approval declined",
    10: "Background check requested",
    11: "Background details filled by renter",
    12: "Background details initiated by owner",
    13: "Background check complete",
    14: "Contract shared by owner",
    15: "Invalid"
  };
  actionMap = {
    14: "Sign Agreement"
  };
  nonActionMap = {
    0: "Awaiting owner's contract",
    1: "Confirmed",
    2: "Declined",
    3: "Blocked",
    4: "Moved out",
    6: "Owner Signed",
    8: "email awaiting approval",
    9: "email approval declined",
    10: "Background check requested",
    11: "Background details filled by renter",
    12: "Background details initiated by owner",
    13: "Background check complete",
    15: "invalid"
  };

  getReportData(propertyRenterId, callback) {
    // loaderService.showLoader(" Fetching Details ");
    this.renterService.getPDFData(propertyRenterId, function (error, data) {
      // this.loaderService.hideLoader();
      if (!error) {
        thisModule.reportData = data;
        // this.$apply();
        callback(data);
      }
    });
  };

  openDownloadDialog(report) {

    console.log(report)
    this.getReportData(report.id, function (data) {
      console.log(data)
      const dialogRef = thisModule.dialog.open(DownloadContractDialog, {
        width: '100vw',
        height: '100vh',
        data: { test: "test", contractForm: data },

      });

      dialogRef.afterClosed().subscribe(result => {
        if (result) {

        }
      })
    });

  }

  signContractDialog() {

    this.currentTab = 0;

    const dialogRef = this.dialog.open(RenterSignContractDialog, {
      width: '100vw',
      height: '100vh',
      data: { contractForm: this.reportData.data },

    });

    dialogRef.afterClosed().subscribe(result => {
      if (result == "success") {
        this.signContract();
      }
    })

    // $mdDialog.show({
    //     controller: 'DialogController',
    //     controllerAs: 'ctrl',
    //     templateUrl: '/views/dialog/renter-sign-contract.html',
    //     parent: angular.element(document.body),
    //     clickOutsideToClose: true,
    //     fullscreen: true,
    //     locals: {contractForm: this.reportData.data, currentTab: this.currentTab, parent: $scope}
    // }).then(function (response) {
    //     if (response == "success") {
    //         this.signContract();
    //     }
    // }, function () {

    // });
  };

  currentTab
  nextTab() {
    this.currentTab += 1;

    if (!this.quill) {
      this.quill = new Quill('#editor-container', {
        modules: {
          toolbar: []
        },
        placeholder: 'Compose an epic...',
        theme: 'snow'  // or 'bubble'
      });
    }
    this.quill.setContents(this.reportData.data.quill);
    this.quill.disable();
  };

  prevTab() {
    this.currentTab -= 1;
  };

  currentReport

  showDialogue(report) {
    this.currentReport = report;
    this.getReportData(report.id, function () {
      thisModule.signContractDialog();
    });
  };

  signContract() {
    let thisModule = this;
    this.renterService.signContract(this.reportData.id, function (error, data) {
      
      if (!error) {
        thisModule.reports.splice(thisModule.reports.indexOf(thisModule.currentReport), 1);
      }
    });
  };
  fetchReports(page) {
    console.log(this.userDataService.user)
    if (this.userDataService.user.isOwner) {
      return this.router.navigate([""])
    }

    if (this.filters["temp_status"] == -1) {
      delete this.filters["status"];
    } else {
      this.filters["status"] = this.filters["temp_status"];
    }

    // loaderService.showLoader("Fetching Reports");
    this.renterService.getContracts(page, this.filters, function (error, data) {
      // loaderService.hideLoader();
      if (!error) {
        thisModule.reports = [];
        thisModule.reports = data.results;
        thisModule.reportsTotal = data.count;
      }
    });
  };


  pageChanged(pageNumber) {
    this.fetchReports(pageNumber);
  };
  // this.$on('event:auth', function (event, data) {
  //     console.log(" I am authenticated");
  //     if (data.authenticated) {
  //         this.userDetails = data.details;
  //         this.fetchReports(1);
  //     } else {
  //         this.reports = [];
  //         this.$apply();
  //     }
  // });

}
