import { Component, ViewChild, OnDestroy } from '@angular/core';
import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';
import { Observable, Subscription } from 'rxjs';
import { map } from 'rxjs/operators';
import { MatSidenav } from '@angular/material'
import { userDataTemplate, UserDataService } from './../../services/user-data.service'

import {
  trigger,
  state,
  style,
  animate,
  transition
} from '@angular/animations';
import { Router } from '@angular/router';

export const hideOverlay =
  trigger('hideOverlay', [
    state('opened', style({ opacity: 0.48 })),
    state('closed', style({ opacity: 0 })),
    transition('* => *', [
      animate(400)
    ])
  ]);


@Component({
  selector: 'app-sidenav',
  templateUrl: './sidenav.component.html',
  styleUrls: ['./sidenav.component.css'],
  animations: [hideOverlay]
})
export class SidenavComponent implements OnDestroy {
  @ViewChild('drawer') drawer: MatSidenav;
  user: userDataTemplate = new userDataTemplate();
  authenticationStateSubscription: Subscription;
  loginMenu = false;

  state = 'closed';

  toggleState() {
    this.state = this.state === 'opened' ? 'closed' : 'opened';
  }

  toggleMenu() {
    this.loginMenu = !this.loginMenu;
  }

  killMenu() {
    this.toggleState();
    this.drawer.toggle();
  }
  isHandset$: Observable<boolean> = this.breakpointObserver.observe(Breakpoints.Handset)
    .pipe(
      map(result => result.matches)
    );

  constructor(private breakpointObserver: BreakpointObserver,
    private userDataService: UserDataService,
    private router: Router
  ) {
    this.authenticationStateSubscription = userDataService.authenticationState$.subscribe(user => {
      this.user = user;
      if (!this.user.authenticated) {
        this.loginMenu = false;
      } else {
        this.loginMenu = true;
      }
    })
  }

  navigate(route) {
    this.killMenu();
    this.router.navigate([route])
  }

  ngOnDestroy() {
    this.authenticationStateSubscription.unsubscribe();
  }

  navigateToReports() {
    console.log(this.userDataService.user.isOwner)
    this.killMenu();
    if (this.userDataService.user.isOwner) {
      this.router.navigate(["/reports"])
    } else {
      this.router.navigate(["/renter-report"])
    }
  }
}
