
export function ContractForm(options) {
    options = options || {};

    let result = {
        property_renter_id: options.property_renter_id,
        city: options.city || "",
        owner_name: options.owner_name || "",
        zip: options.zip || "",
        end_date: options.end_date || "",
        monthly_rent: options.monthly_rent,
        type: options.zip || 1,
        room_label: options.room_label || "",
        country: options.country || "",
        renter_name: options.renter_name || "",
        state: options.state || "",
        late_fee: options.late_fee || "",
        pets: options.pets || false,
        address: options.address || "",
        first_month_rent: options.first_month_rent,
        security_deposit: options.security_deposit,
        start_date: options.start_date || "",
        property_id: options.property_id,
        rooms: options.rooms || 1,
    }
    return result
};

