
import { Component, Inject } from "@angular/core"
import {
    MatDialogRef,
    MAT_DIALOG_DATA
} from "@angular/material"

@Component({
    selector: 'apply-lease',
    templateUrl: './apply-lease.html',
})
export class ApplyLeaseDialog {
    selectedUnit: any
    details: any;
    checkedRooms: any = {}
    constructor(
        public dialogRef: MatDialogRef<ApplyLeaseDialog>, @Inject(MAT_DIALOG_DATA) data: any) {
        this.details = data.details
        this.selectedUnit = data.selectedUnit ? data.selectedUnit : {}

        console.log(this.selectedUnit, this.details)
    }

    selectRoom(id) {
        this.checkedRooms[id] = true;
        console.log(id)
    }
    closeDialog(answer): void {
        if (answer) {
            this.dialogRef.close({ "status": answer, checkedRooms: this.checkedRooms, selectedUnit: this.selectedUnit });
        } else {
            this.dialogRef.close();

        }
    }

}
