export class MapModel {
    Place
    LOCATION_TYPE = {
        ADMIN_1: "administrative_area_level_1",
        LOCALITY: "locality",
        ZIP: "postal_code",
        NAME_TYPES: {
            SHORT: "short_name",
            LONG: "long_name"
        }
    };

    constructor(){}

    extractPlaceDetails = function (data) {
        var components = data["address_components"], types;

        if (!components || components.length < 1) {
            return null;
        }

        let thisModule = this;
        components.forEach(function (component) {
            types = component.types;

            types.forEach(function (type) {
                if (type === thisModule.LOCATION_TYPE.ADMIN_1) {
                    data.state = component[thisModule.LOCATION_TYPE.NAME_TYPES.SHORT];
                } else if (type === thisModule.LOCATION_TYPE.LOCALITY) {
                    data.city = component[thisModule.LOCATION_TYPE.NAME_TYPES.LONG];
                } else if (type === thisModule.LOCATION_TYPE.ZIP) {
                    data.zip = component[thisModule.LOCATION_TYPE.NAME_TYPES.LONG];
                }
            });
        });

        data.state = data.state? data.state: "";
        data.city = data.city? data.city: "";
        data.zip = data.zip? data.zip: "";
        return data;
    };

}