export class FileUploadHelper {
    options: any;
    constructor(options) {
        console.log(options)
        this.options = options;
    }

    stringGen(len) {
        var text = "";
        var charset = "abcdefghijklmnopqrstuvwxyz0123456789";

        for (var i = 0; i < len; i++) {
            text += charset.charAt(Math.floor(Math.random() * charset.length));
        }

        return text;
    }

    upload = function (callback) {
        console.log("WOrking")
        var file = this.options.file;
        var fd = new FormData();
        this.callback = callback;
        this.imageId = new Date().getTime() + this.stringGen(10);
        this.key = this.options.path + this.imageId;
        fd.append('key', this.key);
        fd.append('AWSAccessKeyId', 'AKIAJNYRCTHQWWKAI4WQ');
        fd.append('acl', 'private');
        fd.append("success_action_redirect", "");
        fd.append('policy', this.options.policy);
        fd.append('signature', this.options.signature);
        fd.append('Content-Type', "");
        fd.append("file", file);

        var xhr = new XMLHttpRequest();
        var that = this;
        xhr.upload.addEventListener("progress", function (evt) {
            if (evt.lengthComputable) {
                var percentComplete = Math.round(evt.loaded * 100 / evt.total);
                // $('#circle' + that.options.index).progressCircle({
                //     nPercent: percentComplete,
                //     showPercentText: false,
                //     thickness: 3,
                //     circleSize: 50
                // });
            }
        }, false);
        xhr.addEventListener("load", function (evt) {
            // $('#s-new-tn' + that.options.index).find(".progressBar").remove();
            that.callback(that.imageId);
        }, false);
        xhr.addEventListener("error", this.uploadFailed, false);
        xhr.addEventListener("abort", this.uploadCanceled, false);

        xhr.open('POST', this.options.s3_url, true); //MUST BE LAST LINE BEFORE YOU SEND

        xhr.send(fd);
    };

    uploadFailed = function (evt) {
        console.log("uploadFailed");
    };

    uploadCanceled = function (evt) {
        console.log("uploadCanceled");
    };
}