import { Component, OnInit, OnDestroy } from '@angular/core';
import { RenterServiceService } from '../../../services/renterService/renter-service.service'
import { ActivatedRoute, Router, ChildActivationEnd } from '@angular/router'
import { Subscription } from 'rxjs';

declare let google: any;
@Component({
  selector: 'app-search-property',
  templateUrl: './search-property.component.html',
  styleUrls: ['./search-property.component.css']
})
export class SearchPropertyComponent implements OnInit, OnDestroy {
  suggestions: any = [];
  amenities: any = [];
  propertyResults: any = [];
  selectedCity: any = null;
  stTypes: any = ["university", "city", "zipcode"];
  selectedAmenities: any = [];

  filters: any = {
    searchId: null,
    priceHigh: 15000,
    priceLow: 0,
    sortBy: "price",
    sortValue: 1,
    aptType: 0,
    zipCode: ""
  };
  searched: any = false;
  searchType: any = "university"
  hideFilters: any = false;
  formattedAddress: any = null;
  paramSubscription: Subscription
  mapsInfoWindows: any
  constructor(
    private router: Router,
    private location: ActivatedRoute,
    private renterService: RenterServiceService,
  ) {
    this.paramSubscription = this.router.events.subscribe(val => {
      // console.log(val, val instanceof ChildActivationEnd)
      if (val instanceof ChildActivationEnd) {
        let params = val.snapshot.queryParams;
        console.log(val, params)
        this.setParams(params)
      }
    });
  }

  ngOnInit() {
    console.log(document.getElementById("mapcanvas"))
    this.renterService.getAmenities((err, data) => {
      if (err) {
        console.log("err", err)
      } else {
        this.amenities = data.results
      }
    })
  }

  setParams(params) {
    this.filters.searchId = params["sid"] || null
    this.filters.priceHigh = params["ph"] ? parseInt(params["ph"]) : 15000
    this.filters.priceLow = params["pl"] || 0
    this.filters.sortBy = params["sb"] || "price"
    this.filters.sortValue = params["sv"] ? parseInt(params["sv"]) == 2 ? 2 : 1 : 1
    this.filters.aptType = params["at"] || 0
    this.filters.zipCode = params["zc"] || null
    this.citySearchValue = params["csv"] || "";

    this.searchValue = params["loc"]
    console.log(this.searchValue)
  }

  searchProperty() {

    this.suggestions = [];
    this.selectedCity = this.selectedCity ? this.selectedCity.split(" ").join("_") : "";
    this.citySearchValue = this.formattedAddress;

    function buildInfoWindowContentHtml(property) {
      var template = "<ul style=\"list-style-type:none\">";
      template += "<li><strong>" + property.apt_name + "</strong></li>"
      template += "<li><span>" + property.city + ", " + property.state + "</span></li>"
      return template + "<ul>";
    };

    function getPropertyByMarker(property) {
      // var card = document.getElementById("card-" + property.id);
      // var cardContainer = document.getElementById("card-container");
      // $(card).clearQueue();
      // $(card).stop();
      // $(cardContainer).clearQueue();
      // $(cardContainer).stop();
      // var topOffset = $(card).position().top - 15;
      // var topCurrentScroll = $(cardContainer).scrollTop();
      // var leftOffset = $(card).position().left - 15;
      // var leftCurrentScroll = $(cardContainer).scrollLeft();
      // $(cardContainer).animate({
      // scrollTop: topCurrentScroll + topOffset,
      // scrollLeft: leftCurrentScroll + leftOffset
      // }, 1500);

      // $(card).css({ backgroundColor: "#21CE99" });
      // $(card).animate({ backgroundColor: '#FFFFFF' }, 2000);
    }

    function focusMapOnMarkers(map, properties) {
      var lats = properties.map(p => p.latitude);
      var longs = properties.map(p => p.longitude);

      var latMin = Math.min.apply(Math, lats);
      var longMin = Math.min.apply(Math, longs);
      var latMax = Math.max.apply(Math, lats);
      var longMax = Math.max.apply(Math, longs);

      map.fitBounds(new google.maps.LatLngBounds(
        new google.maps.LatLng(latMin, longMin),
        new google.maps.LatLng(latMax, longMax)
      ));
    }

    function createMap(properties) {

      let latlng = new google.maps.LatLng(39.305, -76.617);
      let mapOptions = {
        center: { lat: properties[0].latitude, lng: properties[0].longitude },
        // center: latlng,
        zoom: 12
      };

      // console.log(document,document.getElementById("mapcanvas"))
      var map = new google.maps.Map(document.getElementById("mapcanvas"), mapOptions);
      focusMapOnMarkers(map, properties);
      thisModule.mapsInfoWindows = [];
      properties.forEach(function (prop) {
        var newMarker = new google.maps.Marker({ position: { lat: prop.latitude, lng: prop.longitude }, icon: "assets/images/graphics/locationPin.png", map: map });
        var infowindow = new google.maps.InfoWindow({
          content: buildInfoWindowContentHtml(prop)
        });
        thisModule.mapsInfoWindows.push(infowindow);
        newMarker.addListener('click', function () {
          getPropertyByMarker(prop);
          thisModule.mapsInfoWindows.forEach(function (infoWindow) {
            infoWindow.close();
          });
          infowindow.open(map, newMarker);
        });
      });
    }

    let reqData = {
      id: this.filters.searchId,
      name: this.selectedCity,
      zip: this.filters.zipCode,
      priceHigh: this.filters.priceHigh,
      priceLow: this.filters.priceLow,
      cityType: this.searchType === "city" ? 1 : 2,
      amenities: this.selectedAmenities.length !== 0 ? this.selectedAmenities.toString() + "," : "",
      sortBy: this.filters.sortBy,
      sortValue: this.filters.sortValue,
      aptType: this.filters.aptType,
      ref: this.searchValue || this.selectedCity
    };

    let thisModule = this;
    let callback = function (error, data) {
      // loaderService.hideLoader();
      thisModule.searched = true;
      thisModule.propertyResults = data.results;
      thisModule.hideFilters = true;
      // $timeout(function () { //Must be in timeout to trigger digest cycle after display none otherwise the map won't fully load
      if (thisModule.propertyResults.length > 0) {
        createMap(thisModule.propertyResults);
      }
      // }, 0);
    };
    this.updateUrl();
    // loaderService.showLoader("Fetching Results");
    if (this.searchType === "university") {
      this.renterService.getPropertiesByUniversity(reqData, callback);
    } else {
      this.renterService.getPropertiesByCity(reqData, callback);
    }

  };

  setAddress($event) {
    this.selectedCity = $event.vicinity;
    this.formattedAddress = $event.formatted_address;
  }

  toggleFilters() {
    this.hideFilters = !this.hideFilters;
  };

  updatePropertyType(value) {
    this.filters.aptType = value;
  }

  updateSelectedAmenities(amenity, value) {
    if (!value) {
      this.selectedAmenities.splice(this.selectedAmenities.indexOf(amenity.id), 1);
    } else {
      this.selectedAmenities.push(amenity.id);
    }

    amenity.selected = value;
  };


  updateSuggestions() {
    let thisModule = this;
    thisModule.suggestions = [];
    this.renterService.getSearchSuggestions(this.searchValue, function (error, data) {
      if (data) {
        thisModule.suggestions = data.universities;
      }
    });
  };

  resetSuggestions() {
    this.suggestions = [];
  };

  changeSearchType() {
    this.resetSuggestions();
    if (this.filters.sortBy === "distance") {
      this.filters.sortBy = "price";
    }
  };

  searchValue: any;
  citySearchValue: any;
  selectUniversity(value) {
    this.searchValue = value.name || this.searchValue;
    this.filters.searchId = value.id || this.filters.searchId;
    this.suggestions = [];
  };

  updateUrl() {
    let filters = this.filters;
    let queryParams = JSON.parse(JSON.stringify(this.location.snapshot.queryParams));

    if (filters.searchId != undefined && filters.searchId != "") {
      queryParams["sid"] = filters.searchId;
    }

    if (filters.priceHigh != undefined) {
      queryParams["ph"] = filters.priceHigh;
    }

    if (filters.priceLow != undefined && filters.priceLow != 0) {
      queryParams["pl"] = filters.priceLow;
    }

    if (filters.sortBy != undefined) {
      queryParams["sb"] = filters.sortBy;
    }

    if (filters.sortValue != undefined) {
      queryParams["sv"] = filters.sortValue;
    }

    if (filters.aptType != undefined) {
      queryParams["st"] = filters.aptType;
    }

    if (filters.zipCode != undefined && filters.zipCode != "") {
      queryParams["zc"] = filters.zipCode;
    }

    console.log(this.searchValue)
    if (this.searchValue != "") {
      queryParams["loc"] = this.searchValue;
    }

    if (this.selectedCity != "") {
      // console.log("test", this.selectedCity)
      queryParams["sc"] = this.selectedCity;
    }

    if (this.citySearchValue != "") {
      queryParams["csv"] = this.citySearchValue;
    }

    if (this.searchType != "university") {
      queryParams["st"] = this.searchType;
    }
    if (this.selectedAmenities.length != 0) {
      queryParams["sa"] = this.selectedAmenities.join();
    }

    this.router.navigate(["/search-property"], { queryParams: queryParams })


  };

  ngOnDestroy() {
    this.paramSubscription.unsubscribe();
  }

  gotoDetails(id) {
    this.router.navigate(["/property-details"], { queryParams: { id: id } })
  }
}
