
import { Component, Inject } from "@angular/core"
import { MatDialogRef, MAT_DIALOG_DATA } from "@angular/material"
import { OwnerService } from "../../../services/owner/owner.service";

@Component({
    selector: 'move-in-confirmation',
    templateUrl: './move-in-confirmation.html'
})
export class MoveInConfirmationDialog {
    // roomDetailsCopy: any = [];
    report: any
    constructor(
        public ownerService: OwnerService,
        public dialogRef: MatDialogRef<MoveInConfirmationDialog>,
        @Inject(MAT_DIALOG_DATA) data: any
    ) {
        this.report = data.report;
        // this.roomDetailsCopy = data.roomDetailsCopy;
    }

    closeDialog(num): void {
        if (num) {
            this.dialogRef.close(num);
        } else {
            this.dialogRef.close();
        }
    }
}
