import { Injectable } from '@angular/core';
import { RequestService } from '../Request/request.service'
import { CookieService } from 'ngx-cookie-service'
declare var $: any
import { Config } from "../../utils/Config";
import { Urls } from '../../utils/Urls'

@Injectable({
  providedIn: 'root'
})
export class CommonService {

  constructor(private requests: RequestService, private cookie: CookieService) { }

  trasXhr;
  ConfigObj = Config()
  UrlsObj = Urls(this.ConfigObj)
  itemsPerPage = this.ConfigObj["ITEMS_PER_PAGE"]
  isRenterLogin () {
    return this.cookie.get('or') == "0";
  };

  getS3Details (callback) {
    this.requests.send(this.UrlsObj["GET_S3_DETAILS"], "GET", {}, false, true, callback).subscribe(data=>{
      callback(null, data)
    }, err=>{
      callback(err, null)
    });
  };

  getMaintenanceById (id, callback) {
    var cb  = function(error, data) {
      if (!error) {
        this.updateMaintenanceData(data);
      }
      callback(error, data);
    };

    var url = this.UrlsObj["MAINTENANCE_DETAILS"] + "/" + id;

    this.requests.send(url, "GET", {}, false, true, cb);
  };

  getTransactions (page, callback, propertyId) {
    propertyId = propertyId || 0;
    if (this.trasXhr) {
      this.trasXhr.abort();
    }

    var cb = function(error, data) {
      this.trasXhr = undefined;
      callback(error, data);
    };

    var url = this.UrlsObj["GET_TRANSACTIONS"] + propertyId + "/" + "?offset=" + (page - 1) * this.itemsPerPage;

    this.trasXhr = this.requests.send(url, "GET", {}, false, true, cb);
  };

  paymentServiceInitiated (type, callback) {
    this.requests.send(this.UrlsObj["PAYMENT_SERVICE_INIT"], "POST", { type: type }, false, true, callback);
  };

  removeFundingSource (type, id, callback) {
    this.requests.send(this.UrlsObj["REMOVE_FUNDING_SOURCE"], "POST", { user_type: type, id: id }, false, true, callback);
  };

  createUnverifiedCustomer (reqData, callback) {
    this.requests.send(this.UrlsObj["CREATE_UNVERIFIED_CUSTOMER"], "POST", reqData, false, true, callback);
  };

}