
import { Component, Inject } from "@angular/core"
import {
    MatDialogRef,
    MAT_DIALOG_DATA
} from "@angular/material"

@Component({
    selector: 'unit-selection',
    templateUrl: './unit-selection.html',
})
export class UnitSelectionDialog {
    errorText: any
    unitDetails: any
    checkedUnits: any
    selectedUnit: any
    selectedIndex: any
    constructor(
        public dialogRef: MatDialogRef<UnitSelectionDialog>, @Inject(MAT_DIALOG_DATA) data: any) {
        this.unitDetails = data.unitDetails;
        this.checkedUnits = data.checkedUnits;
    }

    closeDialog(answer): void {
        this.dialogRef.close();
    }

    selectCheckUnit(i){
        this.checkedUnits[i] = true;
    }


    selectUnit() {
        this.selectedUnit = {};
        this.errorText = null;

        for (var key in this.checkedUnits) {
            if (this.checkedUnits[key]) {
                this.selectedIndex = key;
            }
        }

        if (this.selectedIndex != -1) {
            this.selectedUnit = this.unitDetails[key];
            var allOccupied = true;
            for (var i = 0; i < this.selectedUnit.details.length; i++) {
                allOccupied = allOccupied && this.selectedUnit.details[i].is_occupied;
            }
            if (allOccupied) {
                this.errorText = " Selected unit has no empty rooms  ";
                return;
            }

            this.dialogRef.close({ "status": 'apply', selectedIndex: this.selectedIndex, selectedUnit: this.selectedUnit });

        } else {
            this.errorText = " Please select unit before proceeding ";
            return;
        }
    };

}
