    //     $.cookie('at', response.access_token, { path: '/' });
    //     $.cookie('rt', response.refresh_token, { path: '/' });
    //     $.cookie('ed', new Date().getTime() + (response.expires_in * 1000), { path: '/' });
    //     $.cookie('or', $scope.userType, { path: '/' });

    //     //Logout of google after getting token from server
    //     var auth2 = gapi.auth2;
    //     if (auth2) {
    //       auth2.getAuthInstance().signOut();
    //     }

    //     authService.checkLogin(function (error, data) {
    //       var status = data.status, isOwner = false, isRenter = false;
    //       if (status === 1) {
    //         isOwner = true;
    //       } else if (status === 2) {
    //         isRenter = true;
    //       } else if (status === 3) {
    //         isOwner = true;
    //         isRenter = true;
    //       }

    //       if ($scope.isLogin) {
    //         if ($scope.userType == 0 && !isRenter) {
    //           $scope.detailsError = "Please register as a renter to continue.";
    //           $scope.$apply();
    //         } else if ($scope.userType == 1 && !isOwner) {
    //           $scope.detailsError = "Please register as an owner to continue.";
    //           $scope.$apply();
    //         } else {
    //           $scope.detailsError = undefined;
    //           $scope.$apply();
    //           authService.authenticate(function (error, data) {
    //             authService.updateUserDetails({ isOwner: isOwner, isRenter: isRenter });
