import { Injectable } from '@angular/core';
import { Config } from "../../utils/Config";
import { Urls } from '../../utils/Urls'
import { RequestService } from '../Request/request.service'
import { CookieService } from 'ngx-cookie-service';
import { UserDataService, userDataTemplate} from '../user-data.service'
declare var $: any;

@Injectable({
  providedIn: 'root'
})

export class AuthServiceService {

  constructor(private requests: RequestService, private cookie: CookieService, userDataService: UserDataService) { }
  UrlsObj = Urls(Config())
  userDetails: any;
  allbackAfterLogin;
  authenticate = function (callback) {
    let thisModule = this;
    if (!this.userDetails) {
      if (this.cookie.get('at')) {
        var cb = function (error, data) {
          if (!error) {
            thisModule.userDetails = data;
            setTimeout(function () {
              // this.userDataService.annou
              // $rootScope.$broadcast('event:auth', { details: this.userDetails, authenticated: true });
            }, 0);
          }
          callback(error, data);
        };
        this.requests.send(this.UrlsObj.GET_RENTER_DETAILS, "GET", {}, false, true, cb).subscribe(data=>{
          cb(null, data)
        }, err=>{
          cb(err, null)
        });
      } else {
        callback(null, this.userDetails)
      }
    } else {
      setTimeout(function () {
        // $rootScope.$broadcast('event:auth', { details: this.userDetails, authenticated: true });
      }, 0);
      callback(null, thisModule.userDetails);
    }
  };

  applyCallback = function () {
    var temp;
    if (this.callbackAfterLogin) {
      temp = this.callbackAfterLogin;
      this.callbackAfterLogin = null;
      this.authenticate(temp)
    }
  };

  getToken = function () {
    if (this.cookie.get('ed') != undefined && this.cookie.get('ed') != null && parseInt(this.cookie.get('ed')) < (new Date().getTime() - 5000)) {
      var data = { client_id: null, client_secret: null, grant_type: null, refresh_token: null };
      data.client_id = this.cookie.get('cid');
      data.client_secret = this.cookie.get('csid');
      data.grant_type = "refresh_token";
      data.refresh_token = this.cookie.get('rt');
      $.ajax({
        url: "/o/token/",
        type: "post",
        contentType: "application/x-www-form-urlencoded",
        data: data,
        async: false,
        success: function (data) {
          var response = data;
          this.cookie.set('at', response.access_token);
          this.cookie.set('rt', response.refresh_token);
          this.cookie.set('ed', (new Date().getTime() + (response.expires_in * 1000).toString()));
        }
      });
    }
  };

  logout = function (callback) {
    this.cookie.set("at", null);
    this.cookie.set("rt", null);
    this.cookie.set("ed", null);
    this.cookie.set("or", null);
    this.cookie.set("userdata", null);
    this.userDetails = undefined;

    setTimeout(function () {
      // $rootScope.$broadcast('event:auth', { details: this.userDetails, authenticated: false });
    }, 0);
    callback(this.userDetails);
  };

  checkLogin = function (callback) {
    this.requests.send(this.UrlsObj.CHECK_LOGIN, "GET", {}, false, true, callback).subscribe(data=>{
      callback(null, data);
    }, err=>{
      callback(err, null);
    });
  };

  createRenter = function (callback) {
    this.requests.send(this.UrlsObj.CREATE_RENTER, "POST", { token: this.cookie.get('at') }, false, true, callback).subscribe(data=>{
      callback(null, data)
    }, err=>{
      callback(err, null)
    });
  };

  createOwner = function (callback) {
    this.requests.send(this.UrlsObj.CREATE_OWNER, "POST", { token: this.cookie.get('at') }, false, true, callback).subscribe(data=>{
      callback(null, data)
    }, err=>{
      callback(err, null)
    });
  };

  updateUserDetails = function (details) {
    this.userDetails.isOwner = details.isOwner;
    this.userDetails.isRenter = details.isRenter;
  };

}
