import { Injectable, Type } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { CookieService } from 'ngx-cookie-service';
import { default as TYPES } from '../../utils/Request'

@Injectable({
  providedIn: 'root'
})
export class RequestService {

  constructor(private httpClient: HttpClient, private cookie: CookieService) { }

  send(path, type, jsonObject, isAsync, needAuth, callback) {
    let contentType = "application/json";
    let headers = {
      headers: {
        'content-type': contentType
      }
    };

    if (needAuth) {
      console.log(this.cookie.get('at'))
      headers.headers['Authorization'] = 'Bearer ' + this.cookie.get('at');
    }

    if (type === TYPES["POST"] || type === TYPES["PUT"]) {
      //If its post or put request jsonObject is a compulsory argument
      let data = JSON.stringify(jsonObject);

      //Since this method used for api calls, the content type of data being sent is always json


      //for PUT / POST Request crossDomain must be set to true
      let crossDomain = true;

      if (type === TYPES["POST"]) {
        return this.httpClient.post(path, data, headers)
      }

      return this.httpClient.put(path, data, headers)
    }

    if (type === TYPES["DELETE"]) {
      return this.httpClient.delete(path, headers)
    }

    console.log(path)

    return this.httpClient.get(path, headers)

  };

  sendFormData(path, type, jsonObject, isAsync, needAuth, callback) {
    let params = new URLSearchParams();
    for (let key in jsonObject) {
      params.set(key, jsonObject[key])
    }
    console.log(params)

    let contentType = "application/json";
    let headers = {
      headers: {
        'content-type': contentType
      }
    };

    if (needAuth) {
      console.log(this.cookie.get('at'))
      headers.headers['Authorization'] = 'Bearer ' + this.cookie.get('at');
    }


    if (type === TYPES["POST"] || type === TYPES["PUT"]) {
      //If its post or put request jsonObject is a compulsory argument
      let data = JSON.stringify(jsonObject);

      
      //Since this method used for api calls, the content type of data being sent is always json


      //for PUT / POST Request crossDomain must be set to true
      let crossDomain = true;

      if (type === TYPES["POST"]) {
        console.log(path, params.toString())

        return this.httpClient.post(path, data, headers)
      }

      return this.httpClient.put(path, data, headers)
    }

    if (type === TYPES["DELETE"]) {
      return this.httpClient.delete(path, headers)
    }

    
    return this.httpClient.get(path, headers)

  };
}
