import { Component, OnInit } from '@angular/core';
import { OwnerService } from 'src/app/services/owner/owner.service';
import { RenterServiceService } from 'src/app/services/renterService/renter-service.service';
import { DownloadContractDialog } from '../../dialog/download-contract/download-contract.component';
import { MatDialog } from '@angular/material';
import { AddCardDialog } from '../../dialog/add-card/add-card';
import { AddRentScheduleComponent } from '../../dialog/add-rent-schedule/add-rent-schedule.component';
import { PaymentSetupComponent } from '../../dialog/payment-setup/payment-setup.component';

@Component({
	selector: 'app-collect-rent',
	templateUrl: './collect-rent.component.html',
	styleUrls: ['./collect-rent.component.css']
})
export class CollectRentComponent implements OnInit {
	date: Date = new Date();
	cards = [];
	schedules = [];
	cardDetails = {
		number: null,
		exp_month: null,
		exp_year: null,
		cvc: null,
	};
	stripeAccount: {
		id: string,
		details_submitted: boolean,
		charges_enabled: boolean,
		payouts_enabled: boolean
	} | null = null;
	constructor(private ownerService: OwnerService, private renterService: RenterServiceService, private dialog: MatDialog) { }
	ngOnInit() {
		this.ownerService.getOwnerRentSchedule((err, data) => {
			if (err) {
				alert("Something went wrong while your rent schedules");
				return;
			}
			this.schedules = data.results;
		});
		this.ownerService.getStripeAccount((err, res) => {
			if(err) this.stripeAccount = null;
			else this.stripeAccount = res;
		})
	}
	getNextDateFromDay(day: number) {
		let date = new Date();
		if (date.getDate() > day) {
			date.setMonth((date.getMonth() + 1) % 12)
		}
		date.setDate(day);
		return date;
	}
	openDownloadDialog(contractId: number) {
		this.renterService.getPDFData(contractId, (error, data) => {
			if (error) {
				return alert("Something went wrong while fetching contract data");
			}
			this.dialog.open(DownloadContractDialog, {
				width: '100vw',
				height: '100vh',
				data: { test: "test", contractForm: data, isOwner: true, status: true },

			});
		});

	}

	setupPayment() {
		const dialogRef = this.dialog.open(PaymentSetupComponent, {
			width: '100vw',
		});
		dialogRef.afterClosed().subscribe((result) => {
			console.log(result);
		});
	}
	addRentSchedule() {
		const dialogRef = this.dialog.open(AddRentScheduleComponent, {
			width: '900px'
		});
		dialogRef.afterClosed().subscribe((result) => {
			if (result) {
				this.schedules.push(result);
			}
		})
	}

}
