
import { Component, Inject, OnInit } from "@angular/core"
import * as Quill from 'quill';

import * as jspdf from 'jspdf';
import html2canvas from 'html2canvas';

import {
    MatDialogRef,
    MAT_DIALOG_DATA
} from "@angular/material"
import { OwnerService } from "../../../services/owner/owner.service";
let thisModule;
@Component({
    selector: 'download-contract',
    templateUrl: './download-contract.html',
})
export class DownloadContractDialog implements OnInit {
    contractForm
    loading;
    downloading;
    editing;
    isOwner;
    status
    constructor(
        public dialogRef: MatDialogRef<DownloadContractDialog>, @Inject(MAT_DIALOG_DATA) data: any,
        private ownerService: OwnerService
    ) {

        thisModule = this;
        this.editing = false;
        this.downloading = false;
        this.contractForm = data.contractForm.data;
        this.isOwner = data.isOwner;
        this.status = data.status;
        console.log(this.contractForm)

    }

    editContract() {
        this.quill.enable();
    }

    saveContract() {
        this.editing = false;
        this.quill.disable();

        this.contractForm.quill = this.quill.getContents();

        this.ownerService.createContract(thisModule.contractForm, function (error, data) {
            if (!error) {
                thisModule.contractForm = {};
                thisModule.currentTab = 0;
                thisModule.contracts.splice(thisModule.contracts.indexOf(thisModule.currentReport), 1);
            }
        });
    }

    quill: any;
    ngOnInit() {
        setTimeout(() => {
            // thisModule.captureScreen()

            this.quill = new Quill('#editor-container', {
                modules: {
                    toolbar: [
                        [{ header: [1, 2, false] }],
                        ['bold', 'italic', 'underline'],
                        ['image', 'code-block']
                    ]
                },
                placeholder: 'Compose an epic...',
                theme: 'snow'  // or 'bubble'
            });


            this.quill.disable()
            this.quill.setContents(this.contractForm.quill);
        })
    }

    captureScreen() {
        this.downloading = true;
        // var doc = new jspdf();

        // var printContents = document.getElementById('contentToConvert').textContent;

        // doc.text(10, 20, printContents);
        // doc.text(20, 30, 'This is client-side Javascript, pumping out a PDF.');
        // doc.addPage();
        // doc.text(20, 20, 'Do you like that?');


        // var string = doc.output('datauristring');
        // var iframe = "<iframe width='100%' height='100%' src='" + string + "'></iframe>"
        // var x = window.open();
        // x.document.open();
        // x.document.write(iframe);
        // x.document.close();
        // Output as Data URI
        // doc.output('datauri');

        // return
        // var originalContents = document.body.innerHTML;
        // 
        // document.body.innerHTML = printContents;
        // 
        // window.print();

        // document.body.innerHTML = originalContents;
        // var data = document.getElementById('contentToConvert');

        // html2canvas(data).then(canvas => {
        // Few necessary setting options  
        // var imgWidth = 595; 
        // var pageHeight = 842;
        // var imgHeight = canvas.height * imgWidth / canvas.width;
        // var heightLeft = imgHeight;
        // 
        // const contentDataURL = canvas.toDataURL('image/png')
        // let pdf = new jspdf('p', 'mm', 'a4'); // A4 size page of PDF  
        // var position = 0;
        // pdf.addImage(contentDataURL, 'PNG', 0, position, imgWidth, imgHeight)
        // pdf.save('MYPdf.pdf'); // Generated PDF 
        // this.loading = false;  
        // });

        var quotes = document.getElementById('editor-container');

        html2canvas(quotes).then(canvas => {
            var originalContents = document.body.innerHTML;

            //! MAKE YOUR PDF
            var pdf = new jspdf('p', 'pt', 'letter');

            for (var i = 0; i <= quotes.clientHeight / 990; i++) {
                //! This is all just html2canvas stuff
                var srcImg = canvas;
                var sX = 0;
                var sY = 990 * i; // start 990 pixels down for every new page
                var sWidth = 900;
                var sHeight = 990;
                var dX = 0;
                var dY = 0;
                var dWidth = 900;
                var dHeight = 990;


                // 
                document.body["canvas"] = document.createElement("canvas");
                // 
                // window.print();

                // document.body.innerHTML = originalContents;
                // window.onePageCanvas = document.createElement("canvas");
                document.body["canvas"].setAttribute('width', '1200px');
                document.body["canvas"].setAttribute('height', '990px');
                var ctx = document.body["canvas"].getContext('2d');
                // details on this usage of this function: 
                // https://developer.mozilla.org/en-US/docs/Web/API/Canvas_API/Tutorial/Using_images#Slicing
                ctx.drawImage(srcImg, sX, sY, sWidth, sHeight, dX, dY, dWidth, dHeight);

                // document.body["canvas"].appendChild(canvas);
                var canvasDataURL = document.body["canvas"].toDataURL("image/png", 1.0);

                var width = document.body["canvas"].width;
                var height = document.body["canvas"].clientHeight;

                //! If we're on anything other than the first page,
                // add another page
                if (i > 0) {
                    pdf.addPage(612, 791); //8.5" x 11" in pts (in*72)
                }
                //! now we declare that we're working on that page
                pdf.setPage(i + 1);
                //! now we add content to that page!
                pdf.addImage(canvasDataURL, 'PNG', 20, 40, (width * .62), (height * .62));

            }
            //! after the for loop is finished running, we save the pdf.
            console.log("Home")
            let name = this.contractForm.address + "_unit_contract.pdf" 
            pdf.save(name);

            // document.body.innerHTML = originalContents;
            setTimeout(() => {
                thisModule.downloading = false;
            })
        })
    }

    closeDialog(answer): void {
        if (answer) {
            this.dialogRef.close(answer);
        } else {
            this.dialogRef.close();

        }
    }

}
